.ui-kit {
  &-wrap {
    padding: rem(100) 0 rem(50);
    color: white;
  }
  &-page {
    background-color: black;
  }
  &__headline {
    @include adaptiv-value("font-size", 20, 16, 1);
    color: white;
    padding-bottom: rem(30);
  }
  &__desc {
    padding-bottom: rem(30);
  }
  &__content {

  }
  &__row {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    padding: rem(10);
  }
}