.token-modal {
  position: relative;
  width: 100%;
  max-width: rem(625);
  padding: rem(32) rem(21) rem(35) rem(21);


  margin: 15px;
  max-height: 95vh;
  @include scrollbar-x-block;

  background-color: var(--white);
  box-shadow: 0 0 rem(40) rgba(76, 78, 100, .4);
  border-radius: rem(10);
  @media (min-width: 992px) {
    padding: rem(42) rem(37) rem(40) rem(36);
  }
  &-wrap {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: rgba(76, 78, 100, .5);
  }
  &__close {
    position: absolute;
    top: rem(11.63);
    right: 0;
    width: 100%;
    height: 100%;
    min-width: rem(36);
    min-height: rem(36);
    max-width: rem(36);
    max-height: rem(36);
    @media (min-width: 992px) {
      right: rem(12.48);
    }
    &-ico {
      min-width: rem(24);
      min-height: rem(24);
      max-width: rem(24);
      max-height: rem(24);
      @include contain;
    }
  }
  &__headline {
    font-weight: 500;
    font-size: rem(20);
    line-height: 1.334;
    color: rgba(76, 78, 100, .87);
    @media (min-width: 992px) {
      font-size: rem(24);
    }
  }
  &__desc {
    padding: rem(16) 0;
    font-size: rem(16);
    line-height: 1.5;
    letter-spacing: rem(.15);
    color: rgba(76, 78, 100, .6);
  }
  &__select {
    width: 100%;
    //height: rem(54);
    //padding: 0 rem(17);
    //border: rem(1) solid rgba(76, 78, 100, .3);
    //border-radius: rem(8);
    //@media (min-width: 992px) {
    //  height: rem(56);
    //}
    &+.token-modal__select {
      margin-top: rem(10);
    }
    //option {
      //font-size: rem(16);
      //line-height: 1.5;
      //letter-spacing: rem(.15);
      //color: rgba(58, 53, 65, .38);
    //}
  }
  &__label {
    padding: rem(10) 0 rem(24);
    &-item {
      font-size: rem(16);
      line-height: 1.31;
      letter-spacing: rem(.15);
      color: #9495A2;
      &:not(:last-child) {
        padding-bottom: rem(10);
      }
      b {
        font-weight: 700;
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
  }
  &__btn {

  }
  &__logo {
    min-width: rem(75);
    min-height: rem(44);
    max-width: rem(75);
    max-height: rem(44);
    @include contain;
    @media (max-width: 992px) {
      display: none;
    }
  }
}