.tabs {
  display: flex;
  &-panel {
    display: none;
    &.active {
      display: block;
    }
  }
}
.course__content {
  display: none;
  &.active {
    display: block;
  }
}

.tab-content__item {
  display: none;
  &.active {
    display: block;
  }
}
.type-features__item {
  display: none;
  &.active {
    display: flex;
  }
}
.trial__content-item {
  display: none;
  &.active {
    display: flex;
  }
}
.type__content {
  display: none;
  &.active {
    display: flex;
  }
}

ol.tab-nav,
ul.tab-nav {
  padding-left: 0;
}