.review {
  &-partners {
    //&-wrap {
    //  padding-bottom: rem(140.35);
    //}
    &__head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: rem(80) 0 rem(46.64);
      @media (min-width: 992px) {
        padding: rem(49) 0 rem(29.99);
      }
      @media (min-width: 1600px) {
        padding: rem(70) 0 rem(38.82);
      }
    }
    &__subtitle {
      font-weight: 700;
      font-size: rem(16);
      line-height: 1.64;
      letter-spacing: rem(.15);
      color: var(--grey2);
      text-transform: uppercase;
      @media (max-width: 992px) {
        max-width: 60%;
      }
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &__ico {
      position: relative;
      font-size: rem(9.45622);
      line-height: 2;
      letter-spacing: rem(.215625);
      color: var(--green);
      @media (min-width: 992px) {
        font-size: rem(15.7591);
      }
      @media (min-width: 1600px) {
        font-size: rem(17.25);
      }
      &:before {
        content: '';
        position: absolute;
        top: rem(-8);
        right: calc(50% - rem(17));
        z-index: -1;
        max-width: rem(34);
        max-height: rem(34);
        min-width: rem(34);
        min-height: rem(34);
        width: 100%;
        height: 100%;
        border-radius: 50%;
        overflow: hidden;
        background: linear-gradient(258.85deg, #79AE95 -57.92%, #9CDDBF -33.91%, #62A083 -11.82%, #69A388 11.23%, #A2F1CC 33.32%, #83B89F 55.41%, #A8E4C9 78.46%, #7FB49B 102.47%, #78A992 126.48%);
        @media (min-width: 992px) {
          top: rem(-13);
          right: calc(50% - rem(30));
          max-width: rem(57);
          max-height: rem(57);
          min-width: rem(57);
          min-height: rem(57);
        }
        @media (min-width: 1600px) {
          top: rem(-17);
          max-width: rem(64);
          max-height: rem(64);
          min-width: rem(64);
          min-height: rem(64);
        }
      }
    }
    &__content {
      display: flex;
      flex-wrap: wrap;
      margin: 0 rem(-4.21);
    }
    &__item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      //max-width: rem(272.58);
      //min-width: rem(272.58);
      max-width: calc(100% / 2 - rem(8.42));
      min-width: calc(100% / 2 - rem(8.42));
      max-height: rem(111.26);
      min-height: rem(111.26);
      margin: rem(6.17) rem(4.21);
      padding: rem(29);
      background-color: var(--grey);
      transition: .2s;
      @media (min-width: 1200px) {
        max-width: calc(100% / 5 - rem(8.42));
        min-width: calc(100% / 5 - rem(8.42));
      }
      @media (min-width: 992px) {
        max-height: rem(157.65);
        min-height: rem(157.65);
      }
      &:hover {
        background-color: var(--orange);
        //.review-partners__img {
        //  filter: grayscale(0%);
        //  //mix-blend-mode: initial;
        //  mix-blend-mode: color-dodge;
        //}
        path {
          fill: var(--white);
        }
      }
      svg {
        max-width: 100%;
        max-height: 100%;
        //@include contain;
        object-fit: contain;
      }
    }
    //&__img {
      //max-width: 100%;
      //max-height: 100%;
      //object-fit: contain;
      //filter: grayscale(99%);
      //mix-blend-mode: multiply;
    //}
  }
}