.table-tools {
  &-wrap {
    padding: rem(50) 0;
    @media (max-width: 992px) {
      overflow: hidden;
    }
  }
  &__head {
    display: flex;
    justify-content: space-between;
    padding-bottom: rem(55);
    @media (max-width: 992px) {
      flex-direction: column;
      padding-bottom: rem(30);
    }
    &-elem {
      position: relative;
      height: fit-content;
      width: fit-content;
      margin-right: rem(-28);
      margin-top: rem(70);
      font-size: rem(12.7105);
      line-height: 2;
      letter-spacing: rem(.158882);
      color: #254746;
      transform: rotate(-90deg);
      white-space: nowrap;
      @media (max-width: 992px) {
        display: none;
      }
      &:before {
        content: '';
        position: absolute;
        top: rem(-16);
        right: rem(-25);
        z-index: -1;
        width: rem(56);
        height: rem(56);
        background-color: var(--orange);
        border-radius: 50%;
      }
    }
  }
  &__headline {
    width: 100%;
    max-width: rem(777);
  }
  &__desc {
    width: 100%;
    max-width: rem(322);
    min-width: rem(322);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    color: #38604D;
    @media (min-width: 992px) {
      margin: 0 rem(25);
      font-size: rem(18);
    }
    @media (max-width: 992px) {
      padding-top: rem(15);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
  &__content {
    width: 100%;
    @media (max-width: 992px) {
      @include scrollbar-x-block;
      white-space: nowrap;
    }
  }
  table {
    width: 100%;
    border-radius: rem(6);
    @media (max-width: 992px) {
      min-width: rem(946);
    }
  }
  tr {
    height: rem(54);
  }
  th,
  td {
    padding: 0 rem(18);
  }
  th {
    //white-space: nowrap;
    &:not(:first-child) {
      text-align: center;
    }
    &:nth-child(3) {
      b:after {
        content: '';
        display: inline-block;
        width: rem(18);
        height: rem(18);
        min-width: rem(18);
        min-height: rem(18);
        max-width: rem(18);
        max-height: rem(18);
        margin-left: rem(4);
        background-image: url("../img/ico/Badge.svg");
      }
    }
  }
  thead {
    //position: sticky;
    //top: 0;
    background-color: #F5F5F7;
    font-size: rem(16);
    line-height: 1.5;
    letter-spacing: rem(.15);
    color: rgba(76, 78, 100, .5);
    tr {
      border: rem(1) solid rgba(76, 78, 100, .12);
      border-radius: rem(6) rem(6) 0 0;
    }
    b {
      display: block;
      font-weight: 700;
      font-size: rem(16);
      line-height: 1.33;
      color: rgba(76, 78, 100, .87);
      @media (min-width: 1600px) {
        font-size: rem(18);
      }
    }
  }
  tbody {
    tr {
      border: rem(1) solid rgba(76, 78, 100, .12);
      &:has(.table-tools__subtitle) {
        background-color: rgba(76, 78, 100, .05);
      }
    }
    td {
      &:not(:first-child) {
        text-align: center;
      }
    }
    b {
      padding: rem(3) rem(6);
      font-size: rem(16);
      line-height: 1.12;
      letter-spacing: rem(.16);
      color: var(--green7);
      font-weight: 400;
      background: linear-gradient(0deg, rgba(255, 255, 255, .88), rgba(255, 255, 255, .88)), #508068;
      border-radius: rem(16);
    }
    img {
      max-width: rem(17);
      max-height: rem(17);
      min-width: rem(17);
      min-height: rem(17);
      @include contain;
    }
  }
  tfoot {
    border-left: rem(1) solid rgba(76, 78, 100, .12);
    border-right: rem(1) solid rgba(76, 78, 100, .12);
    border-bottom: rem(1) solid rgba(76, 78, 100, .12);
    td {
      padding: rem(9);
    }
  }
  .btn-ui {
    width: 100%;
    font-size: rem(14) !important;
  }
}