.qa {
  padding-top: rem(130);
  padding-bottom: rem(60.5);
  @media (min-width: 992px) {
    padding-top: rem(75);
    padding-bottom: rem(45);
  }
  @media (min-width: 1600px) {
    padding-top: rem(112);
    padding-bottom: rem(76);
  }
  &__head {
    border-bottom: rem(1) solid var(--dark2);
    cursor: pointer;
    @media (max-width: 992px) {
      align-items: flex-start !important;
    }
    .qa__btn-ico {
      transition: .2s;
      @media (max-width: 992px) {
        margin-top: rem(27);
        margin-bottom: rem(8);
      }
    }
    &.active,
    &:hover {
      .qa__btn-ico {
        transform: rotate(180deg);
        svg {
          path {
            fill: var(--orange);
          }
        }
      }
    }
  }
  &__headline {
    margin-bottom: rem(31);
    @media (min-width: 1600px) {
      margin-bottom: rem(18);
    }
  }
  &__title {
    padding-top: rem(27);
    font-weight: 700;
    font-size: rem(20);
    line-height: 1.23;
    color: var(--dark2);
    @media (min-width: 1200px) {
      font-size: rem(24);
    }
    @media (max-width: 992px) {
      max-width: 85%;
    }
    @media (min-width: 1600px) {
      padding: rem(20) 0 rem(0.23);
      font-size: rem(32);
    }
  }
  &__desc-content {
    padding: rem(28) 0;
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    color: var(--grey2);
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1200px) {
      font-size: rem(22);
    }
  }
}