.guides-hero {
  display: flex;
  align-items: flex-end;
  @media (max-width: 992px) {
    flex-direction: column;
    text-align: center;
  }
  &-wrap {
    color: #FFFFFF;
    background: linear-gradient(233.22deg, rgba(80, 118, 100, 0.7) -39.68%, rgba(120, 173, 148, 0.7) -23.23%, rgba(59, 99, 80, 0.7) -8.1%, rgba(70, 112, 92, 0.7) 7.69%, rgba(47, 85, 67, 0.7) 22.83%, rgba(89, 126, 109, 0.7) 37.96%, rgba(128, 174, 153, 0.7) 53.75%, rgba(80, 114, 98, 0.7) 70.2%, rgba(67, 95, 82, 0.7) 86.65%), #5B8473;
  }
  &__content {
    padding: rem(100) 0 rem(51);
    @media (min-width: 992px) {
      padding: rem(77) 0 rem(89);
    }
    @media (min-width: 1600px) {
      padding: rem(77) 0 rem(140);
    }
  }
  &__headline {
    color: #FFFFFF;
  }
  &__desc {
    width: 100%;
    max-width: rem(615);
    padding: rem(5) 0 rem(30);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    @media (min-width: 992px) {
      padding: rem(17) 0 rem(28);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {;
      max-width: rem(709);
      font-size: rem(22);
    }
  }
  &__search {
    input {
      height: rem(60);
      width: 100%;
      max-width: rem(655);
      padding: 0 rem(19);
      background-color: #FFFFFF;
      border-radius: rem(10);
      &::placeholder {
        font-size: rem(16);
        line-height: 1.85;
        letter-spacing: rem(.229861);
        color: #5D8674;
        opacity: 0.6;
        @media (min-width: 992px) {
          font-size: rem(18);
        }
        @media (min-width: 1600px) {;
          font-size: rem(22);
        }
      }
    }
  }
  &__img {
    max-width: rem(501);
    max-height: rem(368);
    @include contain;
    @media (min-width: 1600px) {
      max-width: rem(533);
      max-height: rem(428);
    }
  }
}