.income {
  position: relative;
  display: flex;
  justify-content: space-between;
  @media (max-width: 992px) {
    flex-direction: column;
  }
  &-wrap {
    display: flex;
    //overflow: hidden;
    //overflow-x: hidden;
    //overflow-y: hidden;
    //@media (min-width: 1600px) {
    //  overflow-x: hidden;
    //}
    background: linear-gradient(241.32deg, #507664 -44.82%, #78AD94 -26.24%, #3B6350 -9.15%, #46705C 8.69%, #2F5543 25.79%, #597E6D 42.88%, #80AE99 60.72%, #507262 79.3%, #435F52 97.88%), #4E7863;
  }
  &__content {
    width: 100%;
    max-width: rem(804);
    //min-width: rem(801);
    padding-top: rem(56);
    padding-bottom: rem(42);
    @media (min-width: 992px) {
      padding-top: rem(81);
      padding-bottom: rem(108);
    }
    @media (min-width: 1600px) {
      max-width: rem(930);
    }
  }
  &__btn {
    @media (max-width: 992px) {
      margin: 0 auto !important;
    }
    &-wrap {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      @media (min-width: 992px) {
        max-width: rem(284);
      }
    }
  }
  &__desc {
    @media (max-width: 992px) {
      display: flex;
    }
    @media (max-width: 1200px) {
      order: -2;
    }
    &-text {
      width: 100%;
      max-width: rem(486);
      font-size: rem(18);
      line-height: 1.64;
      letter-spacing: .15px;
      color: var(--white);
      @media (max-width: 992px) {
        max-width: rem(258);
        min-width: rem(258);
        padding-bottom: rem(40);
        padding-left: rem(25);
        font-size: rem(16);
      }
      @media (min-width: 1200px) {
        margin-left: rem(37);
      }
      @media (min-width: 1600px) {
        max-width: rem(600);
        margin-left: rem(66);
        font-size: rem(22);
      }
    }
    &-wrap {
      display: flex;
      width: 100%;
      max-width: rem(804);
      padding-top: rem(30);
      @media (max-width: 1200px) {
        flex-direction: column;
      }
      @media (min-width: 992px) {
        padding-top: rem(62);
      }
      @media (min-width: 1600px) {
        max-width: rem(932);
      }
      hr {
        @media (max-width: 992px) {
          width: 100%;
          max-width: rem(284);
          margin-left: rem(-205);
        }
      }
    }
  }
  &__img {
    @include contain;
    max-width: rem(260);
    max-height: rem(312);
    margin: 0 auto rem(-75);
    @media (min-width: 992px) {
      position: absolute;
      right: rem(0);
      bottom: rem(-120);
      max-width: rem(494);
      max-height: rem(454);
      margin: 0;
    }
    @media (min-width: 1200px) {
      bottom: rem(-50);
      max-width: rem(544);
      max-height: rem(504);
    }
    @media (min-width: 1400px) {
      bottom: rem(-120);
      max-width: rem(644);
      max-height: rem(604);
    }
    @media (min-width: 1600px) {
      right: rem(-260);
      bottom: rem(-134);
      @include adaptiv-value("max-width", 801, 260, 1);
      @include adaptiv-value("max-height", 752, 312, 1);
    }
  }
}