.sitemap {
  color: white;
  &-wrap {
    padding: rem(100) 0 rem(50);
  }
  &-page {
    background-color: var(--black);
  }
  &__desc {
    padding-bottom: rem(40);
  }
  &__headline {
    @include adaptiv-value("font-size", 20, 16, 1);
    color: white;
    padding-bottom: rem(10);
  }
  li {
    list-style: auto;
  }
  a {
     font-size: rem(18);
     display: block;
     text-transform: uppercase;
    text-decoration: none;
     color: white;
     padding-bottom: rem(15);
      width: fit-content;
      &:hover {
        font-size: rem(19);
        transition: .2s;
        //transform: scale(1.1);
        //padding-left: rem(5);
        color: #fb6d33;
      }
   }
  &__title {
    margin-top: rem(30);
    padding-bottom: rem(10);
  }
}