.type-features {
  padding-left: 0;
  &-wrap {
    .btn {
      margin: 0 auto;
      &:hover {
        border: rem(1) solid var(--orange);
      }
    }

    .feature {
      display: none;
      //&:only-of-type() {
      //}
    }
  }
  &__nav-item {
    &+.type-features__nav-item {
      @media (min-width: 992px) {
        margin-left: 1.5rem;
      }
    }
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: rem(12) 0;
      text-decoration: none;
      color: var(--green6);
      transition: .2s;
    }
  }
}

.variants-hero {
  .btn {
    color: var(--orange);
    @media (max-width: 992px) {
      margin: 0 auto;
    }
    &:hover {
      color: var(--white);
    }
  }
}