.navbar-toggler-wrap,
#weady-nav.navbar--nav-drop form.d-flex {
  .nav-link {
    @media (max-width: 1200px) {
      padding: rem(31) 0;
      font-weight: 600;
      font-size: rem(22);
      line-height: .95;
      letter-spacing: .15px;
      color: #7B7B7B !important;
    }
  }
}
.navbar--nav-drop .navbar-toggler {
  margin-top: rem(20);
}
#weady-nav.navbar {
  &--nav-drop {
    .container {
      @media (max-width: 1200px) {
        max-width: none;
        margin-left: 0;
        margin-right: 0;
      }
    }
    .navbar-nav {
      @media (max-width: 1200px) {
        width: 100%;
        max-height: calc(100vh - rem(80) - rem(180));
        @include scrollbar-y-block;
      }
      .nav-link {
        width: fit-content;
        color: #7B7B7B;
        @media (max-width: 1200px) {
          padding: rem(34) 0 rem(25);
          font-weight: 600;
          font-size: rem(22);
          line-height: .95;
          letter-spacing: .15px;
          color: #7B7B7B;
        }
        &:last-child {
          position: relative;
          color: #7B7B7B;
          transition: .2s;
          &.active,
          &:hover {
            &:before {
              content: '';
              position: absolute;
              bottom: 0;
              left: 0;
              height: rem(4);
              width: 100%;
              background-color: #FB6D33;
              @media (min-width: 1200px) {
                bottom: rem(-16);
              }
            }
          }
        }
      }
    }
    .btn-outline-light {
      color: #7B7B7B;
      border: rem(1) solid #7B7B7B;
    }
  }
  &--simple {
    position: relative;
    z-index: 1;
    background-color: transparent !important;
    background: transparent !important;
  }
  .nav-item {
    @media (max-width: 1200px) {
      padding: 0 rem(19);
      margin-left: 0 !important;
      margin-right: 0 !important;
    }
    .nav-link.dropdown-toggle {
      @media (max-width: 1200px) {
        margin: 0 auto;
      }
    }
  //  &:hover {
  //    & + .main-nav__drop-wrap {
  //      opacity: 1;
  //    }
  //  }
  }
}

.navbar--nav-drop,
#weady-nav-white {
  .orange-button {
    border: rem(1) solid transparent;
    &:hover {
      border: rem(1) solid var(--orange);
    }
  }
}

.nav-item-wrap .nav-item {
  @media (max-width: 1200px) {
    border-bottom: 1px solid #CDCDCD;
  }
}

.navbar-brand {
  svg {
    max-width: rem(139);
    min-width: rem(139);
    max-height: rem(36);
    min-height: rem(36);
    @include contain;
    @media (min-width: 992px) {
      max-width: rem(212.38);
      min-width: rem(212.38);
      max-height: rem(55);
      min-height: rem(55);
    }
  }
}

.navbar {
  &-collapse {
    @media (max-width: 1200px) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: calc(100% - rem(60));
      padding-top: rem(10);
      margin: 0 rem(-12);
    }
    form.d-flex {
      @media (max-width: 1200px) {
        flex-direction: column;
        align-items: center;
        padding-top: rem(20);
      }
    }
    .btn-outline-light {
      @media (max-width: 1200px) {
        width: fit-content;
      }
    }
    .btn {
      @media (max-width: 1200px) {
        min-width: rem(267);
        height: rem(55);
        margin-left: auto !important;
        margin-right: auto !important;
      }
      & + .btn {
        @media (max-width: 1200px) {
          margin-top: rem(20);
        }
      }
    }
  }
  &-brand {
    img {
      @media (max-width: 1200px) {
        max-width: rem(139);
        max-height: rem(36);
        min-width: rem(139);
        min-height: rem(36);
        @include contain;
      }
    }
  }
  &-toggler-wrap {
    @media (max-width: 1200px) {
      display: flex;
    }
  }
}

.navItemWrap {
  .nav-item {
    //@media (max-width: 1200px) {
    //  position: relative;
    //}
    //&:after {
    //  @media (max-width: 1200px) {
    //    content: '';
    //    position: absolute;
    //    top: rem(35);
    //    right: rem(30);
    //    width: rem(20);
    //    height: rem(20);
    //    min-width: rem(20);
    //    min-height: rem(20);
    //    background-image: url("../img/community/arrow.svg");
    //    background-size: contain;
    //    background-repeat: no-repeat;
    //    transform: rotate(90deg);
    //    transition: .2s;
    //  }
    //}
    &.active {
      .nav-link__arr {
        @media (max-width: 1200px) {
          margin-right: rem(-8);
          transform: rotate(270deg);
        }
      }
    }
  }
}

.dropdown-toggle {
  &+.dropdown-menu {
    width: fit-content;
    min-width: auto;
    @media (max-width: 1200px) {
      margin-left: rem(-5);
    }
  }
}

.main-nav {
  @media (max-width: 1200px) {
    height: 100vh;
    padding-bottom: rem(47);
  }
  .navbar {
    @media (max-width: 1200px) {
      height: 100%;
    }
    &>div {
      @media (max-width: 1200px) {
        height: 100%;
        align-content: space-between;
      }
    }
  }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(28, 28, 28, .5);
  }
  &-wrap {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;
    width: 100%;
    padding-top: rem(53);
    background-color: white;
    @media (max-width: 1200px) {
      height: 100vh;
      padding-top: 0;
    }
  }
  &__drop {
    display: flex;
    justify-content: space-between;
    @media (max-width: 1200px) {
      flex-direction: column;
      padding: 0 rem(19);
    }
    //&-bg {
    //  min-height: 100vh;
    //  width: 100%;
    //  background-color: rgba(28, 28, 28, .50);
    //  @media (min-width: 1200px) {
    //    position: absolute;
    //    top: rem(77);
    //    left: 0;
    //    z-index: 1;
    //    //margin-top: rem(7) !important;
    //  }
    //}
    &-wrap {
      width: 100%;
      background-color: white;
      @media (min-width: 1200px) {
        position: absolute;
        top: rem(77);
        left: 0;
        z-index: 1;
        //opacity: 0;
        //transition: .2s;
        margin-top: rem(7) !important;
        border-top: rem(1) solid #CDCDCD;
      }
    }
    &-col {
      width: 100%;
      max-width: rem(318);
      &-wrap {
        width: 100%;
        @media (min-width: 1200px) {
          max-width: 50%;
        }
      }
    }
    &--panel {
      //.main-nav__drop-item-wrap {
      //  width: 100%;
      //  max-width: 50%;
      //}
      position: relative;
      &:before {
        @media (min-width: 1200px) {
          content: '';
          position: absolute;
          top: 0;
          right: -45vw;
          height: 100%;
          width: 50vw;
          background-color: #F9F9F9;
        }
      }
    }
    &-menu {
      width: 100%;
      padding-top: rem(25);
      //padding-bottom: rem(47);
      @media (min-width: 1200px) {
        padding-top: rem(34);
        padding-bottom: rem(22);
      }
    }
    &-headline {
      width: 100%;
      padding-bottom: rem(3);
      font-size: rem(18);
      line-height: 1.4;
      font-weight: 400;
      letter-spacing: 0.218775px;
      color: #313C36;
      border-bottom: rem(1) solid #CDCDCD;
      @media (max-width: 1200px) {
        margin-bottom: rem(15);
        padding-bottom: rem(15);
        line-height: 1.66;
      }
      @media (min-width: 1200px) {
        font-size: rem(20);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &-content {
      display: flex;
      flex-wrap: wrap;
      padding-top: rem(1.5);
      @media (max-width: 1200px) {
        flex-direction: column;
      }
    }
    &-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: rem(275);
      min-width: rem(275);
      @media (min-width: 1200px) {
        max-width: rem(308);
        min-width: rem(308);
      }
      &-wrap {
        padding: rem(20) 0;
        width: 100%;
        max-width: calc(100% / 3);
        @media (min-width: 1200px) {
          padding: rem(27) 0;
        }
      }
    }
    &-title {
      padding-bottom: rem(5);
      font-weight: 700;
      font-size: rem(20);
      line-height: .8;
      letter-spacing: rem(.15);
      color: #313131;
      text-decoration: none;
      @media (min-width: 1200px) {
        padding-bottom: rem(24);
        font-size: rem(22);
      }
      @media (min-width: 1600px) {
        font-size: rem(26);
      }
      &:hover {
        color: #FB6D33;
      }
    }
    &-desc {
      font-size: rem(16);
      line-height: 1.4;
      letter-spacing: rem(.15);
      color: #7B7B7B;
      font-weight: 400;
      @media (min-width: 1200px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &-panel {
      padding: rem(40) rem(21) rem(94);
      background-color: #F9F9F9;
      @media (max-width: 1200px) {
        margin: 0 rem(-19);
        padding-bottom: rem(21);
      }
      @media (min-width: 1200px) {
        padding: rem(25) rem(73) rem(100);
        border-left: rem(1) solid #CDCDCD;
      }
      &-item {
        max-width: rem(300);
        min-width: rem(300);
        padding: rem(20) 0;
        //@media (min-width: 668px) {
        //  min-width: rem(300);
        //}
      }
      &-head {
        display: flex;
        align-items: center;
      }
      &-ico {
        max-height: rem(40);
        max-width: rem(35);
        min-height: rem(40);
        min-width: rem(35);
        margin-right: rem(19);
        @include contain;
      }
      &-title {
        font-weight: 700;
        font-size: rem(20);
        line-height: .8;
        letter-spacing: .15px;
        color: #313131;
        text-decoration: none;
        //white-space: nowrap;
        @media (min-width: 1200px) {
          font-size: rem(22);
        }
        @media (min-width: 1600px) {
          font-size: rem(26);
        }
        &:hover {
          color: #fb6d33;
        }
      }
      &-desc {
        padding-top: rem(22);
        font-size: rem(16);
        font-weight: 400;
        color: #7B7B7B;
        @media (min-width: 1200px) {
          padding-left: rem(53);
          font-size: rem(18);
        }
        @media (min-width: 1600px) {
          font-size: rem(22);
        }
      }
    }
  }
}

.navbar-brand img {
  @media (max-width: 768px) {
    height: rem(40);
    min-height: rem(40);
    max-height: rem(40);
  }
}

.navbar .navbar-toggler {
  @media (max-width: 768px) {
    display: flex;
    align-items: center;
    height: rem(40);
  }
}

.nav-link__arr {
  margin-right: rem(37);
  transform: rotate(90deg);
  transition: .2s;
  @media (min-width: 1200px) {
    display: none;
  }
}

.navLinkWrap {
  @media (max-width: 1200px) {
    display: flex;
    justify-content: space-between;
  }
}