.weady-partners {
  padding-top: rem(115);
  padding-bottom: rem(59);
  @media (min-width: 1600px) {
    padding-top: rem(142);
    padding-bottom: rem(92);
  }
  &__headline {
    color: #204242;
  }
  .partner-item {
    width: 100%;
    max-width: rem(350);
    @media (max-width: 992px) {
      margin: 0 auto;
      padding-bottom: rem(64);
    }
    @media (min-width: 1600px) {
      max-width: rem(377);
    }
  }
}