.trial {
  &-wrap {
    padding-top: rem(50);
    @media (min-width: 992px) {
      padding: rem(50) 0;
    }
    @media (max-width: 992px) {
      overflow: hidden;
    }
  }
  &__headline {
    @media (min-width: 992px) {
      text-align: center;
    }
  }
  &__content {
    &-wrap {
    }
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    padding: rem(22) 0 em(31);
    text-align: center;
    @media (min-width: 992px) {
      padding: rem(22) 0 em(55);
    }
    @media (min-width: 1600px) {
      padding: rem(50) 0 em(76);
    }
    @media (max-width: 992px) {
      @include scrollbar-x-block;
      //margin-right: rem(-12);
    }
    //@media (min-width: 768px) and (max-width: 992px) {
    //  margin-right: rem(-147);
    //}
    //@media (min-width: 576px) and (max-width: 768px) {
    //  margin-right: rem(-124);
    //}
    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: rem(50);

      font-weight: 700;
      font-size: rem(18);
      line-height: 1.17;
      letter-spacing: rem(.15);
      text-decoration: none;
      color: #224343;
      @media (min-width: 992px) {
        height: rem(55);
      }
      @media (min-width: 1600px) {
        height: rem(60);
      }
      &-wrap {
        width: 100%;
        max-width: rem(330);
        margin-right: rem(13.67);
        background-color: #CDDED6;
        border-radius: rem(10);
        white-space: nowrap;
        @media (min-width: 1200px) {
          min-width: rem(330);
          margin-right: 0;
        }
        @media (min-width: 992px) {
          max-width: calc(100% / 3 - rem(13.33));
        }
        @media (max-width: 992px) {
          padding: 0 rem(15);
        }
        &:hover,
        &.active {
          background-color: #5D8674;
          box-shadow: 0 rem(7) rem(67) rgba(70, 92, 91, .16);
          @media (min-width: 992px) {
            box-shadow: 0 rem(49) rem(67) rgba(70, 92, 91, .16);
          }
          .trial__nav-item {
            color: var(--white);
          }
        }
      }
    }
  }
  &__content {
    &-item {
      justify-content: space-between;
      @media (max-width: 992px) {
        flex-direction: column;
      }
    }
  }
  &__deac {
    width: 100%;
    @media (min-width: 992px) {
      max-width: 50%;
    }
    @media (max-width: 992px) {
      padding-bottom: rem(39);
    }
  }
  &__title {
    padding-bottom: rem(28);
    font-weight: 900;
    font-size: rem(26);
    line-height: .66;
    letter-spacing: rem(.15);
    color: var(--green-dark);
    @media (min-width: 992px) {
      font-size: rem(24);
    }
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
  }
  hr {
    width: 100%;
    margin: 0;
    //max-width: rem(543);
    @media (min-width: 992px) {
      max-width: 81%;
    }
  }
  ol {
    padding: rem(35) 0  rem(39)  rem(39);
    @media (min-width: 992px) {
      padding: rem(35) 0  rem(60)  rem(39);
    }
    @media (min-width: 1600px) {
      padding: rem(39) 0  rem(39)  rem(39);
    }
  }
  li {
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    color: var(--grey2);
    //list-style: auto;
    list-style: decimal;
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
    b {
      font-weight: 700;
    }
  }
  &__btn {
    min-width: rem(216);
    @media (min-width: 992px) {
      min-width: rem(267);
    }
  }
  &__img {
    max-width: 100%;
    max-height: 100%;
    @include contain;
    &-wrap {
      width: 100%;
      height: fit-content;
      padding: rem(19.25) rem(21.16) rem(21.58) rem(22);
      background-color: var(--orange2);
      border-radius: rem(10);
      @media (min-width: 992px) {
        max-width: 50%;
        margin-left: rem(36);
        padding: rem(30) rem(30) rem(33) rem(27);
      }
      @media (min-width: 1600px) {
        padding: rem(57) rem(30);
      }
    }
  }
}