.blog {
  &-wrap {
    padding-top: rem(61);
    @media (min-width: 992px) {
      padding-top: rem(85);
    }
    @media (min-width: 1600px) {
      padding-top: rem(117);
    }
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media (min-width: 992px) {
      justify-content: space-between;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: rem(393);
    padding-bottom: rem(42);
    text-decoration: none;
    color: var(--white);
    @media (min-width: 992px) {
      max-width: calc(100% / 2 - rem(32));
      padding-bottom: rem(60);
    }
    @media (min-width: 1200px) {
      max-width: calc(100% / 3 - rem(32));
    }
    @media (min-width: 1600px) {
      max-width: calc(100% / 3 - rem(36));
    }
  }
  &__img {
    max-width: rem(392);
    max-height: rem(260);
    min-height: rem(260);
    @include cover;
    background-color: var(--orange);
    border-radius: rem(10);
    @media (min-width: 1600px) {
      max-width: rem(430);
      max-height: rem(286);
      min-height: rem(286);
    }
  }
  &__label {
    padding: rem(33.76) 0 rem(13);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.110124);
    text-transform: uppercase;
    color: #A8A8A8;
    @media (min-width: 992px) {
      font-size: rem(22);
    }
    @media (min-width: 1600px) {
      padding: rem(37) 0 rem(15);
    }
  }
  &__title {
    font-weight: 700;
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.110124);
    color: #204242;
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
  &__desc {
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.110124);
    color: #204242;
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
}