.tariff {
  &-hero {
    &__btn {
      margin: 0 auto;
    }
    &__desc {
      padding-top: rem(10);
      padding-bottom: rem(20);
      @media (min-width: 1600px) {
        padding-bottom: rem(35);
      }
    }
    &__content {
      position: relative;
      overflow: hidden;
    }
    &__bg {
      &-img {
        position: absolute;
        top: rem(42);
        left: 0;
        max-width: rem(35);
        max-height: rem(35);
        @include contain;
        @media (min-width: 992px) {
          top: rem(70);
        }
      }
      &-desc {
        position: absolute;
        top: rem(39);
        right: rem(2);
        font-size: rem(10.386);
        line-height: 1.31;
        letter-spacing: rem(.15);
        color: #FEFEFE;
        transform: rotate(-90deg);
        @media (min-width: 992px) {
          top: rem(58);
          font-size: rem(16);
        }
      }
    }
    &__nav {
      position: absolute;
      top: 50%;
      right: rem(12);
      @media (min-width: 992px) {
        top: 33%;
        right: rem(39.5);
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        z-index: 0;
        width: rem(1);
        height: rem(103.84);
        background-color: var(--white);
        @media (min-width: 992px) {
          height: rem(357);
        }
      }
      &-img {
        position: absolute;
        top: rem(42);
        right: rem(-12);
        z-index: 1;
        max-width: rem(23);
        max-height: rem(23);
        @include contain;
        width: rem(23);
        height: rem(23);
        @media (min-width: 992px) {
          top: rem(110);
          right: rem(-39.5);
          max-width: rem(79);
          max-height: rem(79);
          width: rem(79);
          height: rem(79);
        }
      }
    }
  }
}

.robot-one-page  {
  max-width: rem(933);
  max-height: rem(429);
  margin-top: rem(33);
  @media (min-width: 1600px) {
    margin-top: rem(38);
  }
  @include contain;
  @media (min-width: 1600px) {
    max-height: rem(507);
    max-width: rem(1091);
  }
}

.weady-start-create-site-block-header {
  @media (max-width: 768px) {
    max-width: 80%;
  }
}