.btn-ui {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 rem(20);
  cursor: pointer;
  text-align: center;

  font-weight: 600;
  font-size: rem(18);
  line-height: 2;
  letter-spacing: .744262px;
  border-radius: rem(10);
  text-decoration: none;
  white-space: nowrap;
  //transition: .2s;
  @media (min-width: 992px) {
    font-size: rem(18);
  }
  @media (min-width: 1600px) {
    font-size: rem(22);
  }
  &--orange {
    background-color: var(--orange);
    color: var(--white);
  }
  &--orange-bg-light {
    &:hover {
      background-color: transparent;
      color: var(--orange);
      border: rem(1) solid var(--orange);
    }
  }
  &--orange-bg-dark {
    &:hover {
      background-color: var(--white);
      color: var(--orange);
      //border: rem(1) solid var(--orange);
    }
  }
  &--bg-white {
    &:hover {
      background-color: var(--white);
      border: rem(1) solid var(--orange);
      color: var(--orange);
    }
  }
  &--bg-dark {
    &:hover {
      background-color: transparent;
      border: rem(1) solid var(--white);
      color: var(--white);
    }
  }
  &--white {
    background-color: var(--white);
    color: var(--dark);
    //&:hover {
    //  background-color: transparent;
    //  border: rem(1) solid var(--white);
    //  color: var(--white);
    //}
  }
  &--white-oreage {
    background-color: var(--white);
    color: var(--orange);
    &:hover {
      background-color: var(--orange);
      border: rem(1) solid var(--white);
      color: var(--white);
    }
  }
  &--border-green {
    border: rem(1) solid var(--green7);
    color: var(--green7);
    border-radius: rem(8);

    font-weight: 500;
    font-size: rem(14);
    line-height: 1.71;
    letter-spacing: rem(.4);
    text-transform: uppercase;
    color: var(--green7);
    &:hover {
      background-color: var(--green7);
      color: var(--white);
    }
  }
  &--border-black {
    border: rem(1) solid var(--black2);
    color: var(--black2);
    border-radius: rem(8);

    font-weight: 700;
    font-size: rem(16);
    line-height: 2.03;
    letter-spacing: rem(.744262);
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
    &:hover {
      background-color: var(--black2);
      color: var(--white);
    }
  }
  &--green {
    //width: 138px;
    height: rem(38);
    background-color: #508068;
    box-shadow: 0 rem(4) rem(6) rem(-4) rgba(58, 53, 65, 0.1), 0 rem(4) rem(10) rem(-4) rgba(58, 53, 65, 0.08), 0 rem(4) rem(8) rem(-4) rgba(58, 53, 65, 0.16);
    border-radius: 5px;
    color: var(--white);
    font-weight: 400;
    font-size: rem(14) !important;
    line-height: 1.71;
    letter-spacing: rem(.4);
  }
  &--text-up {
    text-transform: uppercase;
  }
  &--50 {
    height: rem(50);
  }
  &--xs {
    height: rem(55);
  }
  &--md {
    height: rem(60);
  }
}