.selectize--row {
  display: flex;

  @media (max-width: em(768)) {
    flex-direction: column;
  }
  .selectize {
    &:not(:last-child) {
      margin-right: rem(16);
      @media (max-width: em(768)) {
        margin-bottom: rem(10);
      }
    }
  }
}

.token-modal__select {
  width: 100%;
  &.selectize-control.single .selectize-input {
    height: rem(54);
    padding: 0 rem(17);
    background-color: transparent;
    box-shadow: none;
    background-image: none;
    color: var(--black-1);
    border-radius: rem(8);
    border-color: rgba(76, 78, 100, .3);
    @media (min-width: 992px) {
      height: rem(56);
    }
    .item {
      display: flex;
      align-items: center;
      height: rem(54);
      font-size: rem(16) !important;
      line-height: 1.5 !important;
      letter-spacing: rem(.15) !important;
      color: rgba(58, 53, 65, .38) !important;
    }
    .selectize-dropdown.single {
      border-color: var(--grey-2);
    }
    &.input-active {
      border-radius: rem(5) rem(5) 0 0;
      &:after {
        top: 50%;
        transform: rotate(180deg);
      }
    }
    &:after {
      top: 50%;
      width: rem(9);
      height: rem(9);
      min-width: rem(9);
      min-height: rem(9);
      background-size: contain;
      background-repeat: no-repeat;
      background-image: url("../img/arr/select.svg");
      transform: rotate(0deg);
      border: none;
      transition: transform .05s;
    }
  }
  .option {
    display: flex;
    align-items: center;
    padding: 0 rem(17);
    height: rem(36) !important;
    font-size: rem(16) !important;
    line-height: 1.5 !important;
    letter-spacing: rem(.15) !important;
    color: rgba(76, 78, 100, 0.87) !important;
    background-color: var(--white);




    &.active {
      background-color: rgba(76, 78, 100, .05);
    }
  }
  .selectize {
    &-dropdown{
      top: rem(60) !important;
      border-radius: rem(10);
      //border-color: rgba(76, 78, 100, .3);
      border: rem(1) solid rgba(76, 78, 100, .3) !important;
    }
  }
}