.transfer {
  &__content {
  padding: rem(55) rem(30) 0;
    @media (min-width: em(992)) {
      padding: rem(55) rem(57) rem(48);
    }
    &-wrap {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      border-radius: rem(10);
      border: rem(1) solid #D4D4D4;
      background-color: #CDDED6;
      @media (max-width: em(992)) {
        flex-direction: column;
      }
    }
  }
  &__title {
    width: 100%;
    max-width: rem(581);
    color: #2B4D4A;
    font-size: rem(28);
    font-weight: 900;
    line-height: 1;
    letter-spacing: rem(.15);
    @media (min-width: em(1400)) {
      font-size: rem(32);
    }
  }
  &__desc {
    width: 100%;
    max-width: rem(581);
    padding: rem(15) 0;
    color: #345852;
    font-size: rem(16);
    line-height: 1.6;
    letter-spacing: rem(.15);
    @media (min-width: em(1200)) {
      font-size: rem(18);
    }
    @media (min-width: em(1600)) {
      font-size: rem(22);
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    @media (max-width: em(992)) {
      flex-wrap: wrap;
    }
    &-btn {
      min-width: 56%;
    }
    &-text {
      padding: rem(19);
      color: #7B7B7B;
      font-size: rem(16);
      line-height: 1.6;
      letter-spacing: rem(.15);
      white-space: nowrap;
      @media (min-width: em(1200)) {
        font-size: rem(18);
      }
      @media (min-width: em(1600)) {
        font-size: rem(22);
      }
    }
  }
  &__img {
    max-width: rem(420);
    min-height: 100%;
    @include contain;
    @media (min-width: em(992)) {
      margin-right: rem(54);
    }
    @media (min-width: em(1200)) {
      margin-right: rem(108);
    }
    @media (min-width: 992px) and (max-width: 1200px){
      max-width: 35%;
    }
    @media (max-width: em(992)) {
      margin: 0 auto;
    }
  }
}

.transfer-wrap {
  &+.weady-join-beta {
    margin-top: 6rem !important;
  }
}

.bunner-benefits {
  .dark-green-block,
  .green-block {
    @media (max-width: 1200px) {
      padding-left: rem(33.6) !important;
      padding-right: rem(50.4) !important;
    }
    @media (max-width: 992px) {
      padding-left: rem(30) !important;
      padding-right: rem(30) !important;
    }
  }
}
.weady-get-start-item-content {
  @media (max-width: 1200px) {
    padding-left: rem(33.6) !important;
    padding-right: rem(50.4) !important;
  }
  @media (max-width: 992px) {
    padding-left: rem(30) !important;
    padding-right: rem(30) !important;
  }
}