.earn {
  &-wrap {
    padding-top: rem(18);
  }
  &__btn {
    width: fit-content;
    min-width: rem(204);
    max-width: rem(204);
    font-size: rem(16) !important;
    @media (max-width: 768px) {
      min-width: auto;
      width: auto;
      padding: 0 rem(15);
    }
    @media (max-width: 992px) {
      height: rem(55);
    }
  }
  &__headline {
    padding-bottom: rem(32);
    color: var(--dark);
    @media (max-width: 768px) {
      max-width: 75%;
    }
  }
  &__desc {
    padding-bottom: rem(60);
  }
  &__content {
    max-width: rem(235);
    @media (max-width: 992px) {
      padding: rem(37) 0 rem(41) 0;
    }
  }
  &__img {
    //max-width: rem(141);
    max-width: 35%;
    max-height: rem(182);
    @include contain;
    width: auto;
    @media (min-width: 1600px) {
      max-width: rem(170);
      max-height: rem(285);
    }
  }

  &-item {
    display: flex;
    justify-content: space-between;
    background: #FD804C;
    border-radius: rem(7);
    color: white;
    min-height: rem(293);
    &-title {
      width: 100%;
      font-weight: 700;
      font-size: rem(20);
      line-height: 1.1;
      letter-spacing: rem(.15);
      @media (min-width: 992px) {
        font-size: rem(24);
      }
      @media (min-width: 1600px) {
        font-size: rem(32);
        line-height: 1;
      }
    }
  }
}

.ai-website-builder-class-earn-label {
  &-2 {
    .circle-green-gradient-small {
      width: rem(24);
      height: rem(24);
      background: linear-gradient( 258.85deg,
                  #79AE95 -57.92%,
                  #9CDDBF -33.91%,
                  #62A083 -11.82%,
                  #69A388 11.23%,
                  #A2F1CC 33.32%,
                  #83B89F 55.41%,
                  #A8E4C9 78.46%,
                  #7FB49B 102.47%,
                  #78A992 126.48%
      );
      border-radius: 50px;
      z-index: -1;
      @media (min-width: 922px) {
        width: rem(48);
        height: rem(48);
      }
    }
    span {
      font-size: rem(4.98689);
      @media (min-width: 922px) {
        font-size: rem(9.93785);
      }
    }
    .to-130 {
      @media (max-width: 922px) {
        transform-origin: 75%
      }
    }
  }
  &-1 {
    .circle-green-gradient {
      width: rem(42);
      height: rem(42);
      border-radius: 61.9492px;
      background: linear-gradient( 258.85deg,
                  #79AE95 -57.92%,
                  #9CDDBF -33.91%,
                  #62A083 -11.82%,
                  #69A388 11.23%,
                  #A2F1CC 33.32%,
                  #83B89F 55.41%,
                  #A8E4C9 78.46%,
                  #7FB49B 102.47%,
                  #78A992 126.48%);
      z-index: -1;
      @media (min-width: 922px) {
        width: rem(83);
        height: rem(83);
      }
    }
    span {
      font-size: rem(8.63746);
      margin-left: rem(-15);
      @media (min-width: 922px) {
        font-size: rem(17.25);
        margin-left: rem(-30);
      }
    }
  }
}