.work-chat {
  &-wrap {
    padding-top: rem(48);
  }

  &__headline {
    width: 100%;
    max-width: rem(365);
    @media (min-width: 768px) {
      max-width: rem(1144);
    }
  }

  &__contetn {
    display: flex;
    align-content: flex-start;
    width: 100%;
    max-width: rem(948);
    min-width: 55%;
    margin-left: rem(-10);
    margin-top: rem(-13);
    @media (min-width: 992px) {
      flex-wrap: wrap;
      margin-left: rem(-20);
    }
    @media (min-width: 1420px) {
      min-width: 69%;
    }
    @media (max-width: 992px) {
      @include scrollbar-x-block;
      padding-bottom: rem(40);
    }
    &-wrap {
      display: flex;
      justify-content: space-between;
      padding-top: rem(35);
      @media (min-width: 992px) {
        padding-top: rem(63);
        padding-bottom: rem(50);
      }
      @media (max-width: 1200px) {
        flex-direction: column;
      }
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: rem(262);
    min-width: rem(210);
    margin: 0 rem(10);
    padding: rem(27) rem(27) rem(24);
    background-color: var(--green-light);
    border-radius: rem(10);
    text-align: center;
    color: var(--dark3);
    letter-spacing: rem(.218775);
    @media (min-width: 992px) {
      margin: rem(14) rem(20);
      min-width: auto;
      max-width: calc( 100% / 3 - rem(54) );
      //height: fit-content;
      height: calc(100% / 2 - rem(29));
    }
    @media (min-width: 1400px) {
      height: calc(100% / 2 - rem(48));
    }
    &-img {
      max-width: rem(155);
      min-width: rem(155);
      max-height: rem(155);
      min-height: rem(155);
      @include cover;
      border-radius: 50%;
      overflow: hidden;
      @media (min-width: 1200px) {
        max-width: rem(115);
        min-width: rem(115);
        max-height: rem(115);
        min-height: rem(115);
      }
      @media (min-width: 1400px) {
        max-width: rem(155);
        min-width: rem(155);
        max-height: rem(155);
        min-height: rem(155);
      }
    }
    &-desc {
      padding-top: rem(21);
    }
  }
  &__title {
    font-weight: 700;
    font-size: rem(24);
    line-height: 1.09;
    white-space: nowrap;
    @media (min-width: 992px) {
      font-size: rem(32);
    }
    @media (min-width: 1200px) {
      font-size: rem(22);
    }
    @media (min-width: 1400px) {
      font-size: rem(32);
    }
    &:after {
      content: '';
      display: inline-block;
      width: rem(18);
      height: rem(18);
      min-width: rem(18);
      min-height: rem(18);
      max-width: rem(18);
      max-height: rem(18);
      background-color: var(--green3);
      margin-left: rem(7);
      border-radius: 50%;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: rem(16);
    line-height: 2.19;
    @media (min-width: 1200px) {
      font-size: rem(14);
      line-height: 1.19;
    }
    @media (min-width: 1400px) {
      font-size: rem(16);
      line-height: 2.19;
    }
  }
  &__img {
    max-width: 100%;
    object-fit: contain;
    &-wrap{
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      width: 100%;
      max-width: rem(477);
      height: fit-content;
      padding: rem(46) rem(25) rem(34) rem(27);
      background-color: var(--green-light);
      border-radius: rem(10);
      border: rem(1) solid #5D8674;
    }
    &-desc{
      padding-top: rem(40);
      font-size: rem(18);
      line-height: 1.64;
      text-align: center;
      letter-spacing: rem(.15);
      color: #6E8E8E;
      @media (min-width: 992px) {
        font-size: rem(20);
      }
    }
  }
}