.team {
  &-wrap {
    padding: rem(67) 0 rem(60.7);
    @media (min-width: 992px) {
      padding: rem(114) 0 rem(95);
      text-align: center;
    }
    @media (min-width: 1600px) {
      padding: rem(131) 0 rem(91);
    }
  }
  &__content {
    display: flex;
    padding-top: rem(27);
    text-align: center;
    @media (min-width: 992px) {
      padding-top: rem(40);
    }
    @media (min-width: 1600px) {
      padding-top: rem(90);
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: rem(262);
    min-width: rem(262);
    margin-right: rem(15.38);
    padding: rem(35.14) rem(12.28) 0;
    background-color: var(--green4);
    border-radius: rem(10);
    color: var(--white);
    letter-spacing: rem(.359125);
    @media (min-width: 992px) {
      max-width: rem(392);
      min-width: rem(392);
      margin-right: rem(22.74);
      padding: rem(52.56) rem(18.37) 0;
    }
    @media (min-width: 1600px) {
      max-width: rem(449);
      min-width: rem(449);
      margin-right: rem(26);
      padding: rem(60) rem(33) 0;
    }
    &:hover {
      .team__img {
        mix-blend-mode: normal;
      }
    }
  }
  &__title {
    font-weight: 600;
    font-size: rem(20);
    line-height: .9;
    @media (min-width: 992px) {
      font-size: rem(24);
    }
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
  }
  &__subtitle {
    padding-bottom: rem(9.4);
    font-size: rem(16);
    line-height: 1.2;
    @media (min-width: 992px) {
      padding: rem(17) 0 rem(32.44);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
      padding: rem(19.6) 0 rem(37.24);
    }
  }
  &__img {
    max-width: rem(222.84);
    max-height: rem(234.54);
    @include contain;
    height: auto;
    mix-blend-mode: luminosity;
    transition: .4s;
    @media (min-width: 992px) {
      max-width: rem(333.29);
      max-height: rem(350.79);
    }
    @media (min-width: 1600px) {
      max-width: rem(381);
      max-height: rem(401);
    }
  }
}