.sliderSteps {
  .slick-dots {
    bottom: rem(20);
  }
  li {
    height: rem(23);
    width: rem(23);
    margin: 0 rem(3);
    &.slick-active button {
      background-color: #fb6d33;
    }
    button {
      height: rem(23);
      width: rem(23);
      border: rem(4) solid #fb6d33;
    }
  }
}

