.wrap {
  max-width: rem(1920);
  min-width: rem(320);
  margin: 0 auto;
  padding-left: rem(15);
  padding-right: rem(15);
  width: 100%;
}

.inner {
  width: 100%;
  max-width: rem(1573);
  min-width: rem(320);
  padding-left: rem(15);
  padding-right: rem(15);
  margin: 0 auto;
  @media (min-width: em(992)) {
    padding-left: rem(34);
    padding-right: rem(34);
  }
}

.title {
  font-weight: 900;
  line-height: 1.1;
  //font-family: var(--fnt_main);
  &--sm {
    font-size: rem(24);
    //line-height: 1.3;
    //font-weight: 900;
    @media (min-width: em(992)) {
      font-size: rem(42);
    }
  }
  &--md {
    font-size: rem(32);
    @media (min-width: em(992)) {
      font-size: rem(64);
    }
  }
  &--lg {
    font-size: rem(42);
    letter-spacing: rem(.15);
    line-height: 1.1;
    @media (min-width: 992px) {
      font-size: rem(64);
    }
    @media (min-width: em(1600)) {
      font-size: rem(76);
    }
  }
  &--center {
    text-align: center;
  }
  &--dark {
    color: var(--dark);
  }
  &--white {
    color: var(--white);
  }
  &--grad {
    background: linear-gradient(98.08deg, rgba(80, 128, 104, 0.87) -0.92%, rgba(0, 38, 38, 0.87) 84.18%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  //&--bold {
  //  font-weight: 900;
  //}
  &--line {
    display: inline-block;
    width: fit-content;
    vertical-align: middle;
    &:after {
      content: '';
      display: inline-block;
      vertical-align: middle;
      height: rem(31);
      margin-left: rem(15);
      border-radius: rem(28);
      background: linear-gradient(325.43deg, #79AE95 12.6%, #9CDDBF 23.34%, #62A083 33.22%, #69A388 43.53%, #A2F1CC 51.93%, #83B89F 63.29%, #A8E4C9 73.6%, #7FB49B 84.34%, #78A992 95.08%);
      @media (min-width: 992px) {
        height: rem(57);
        margin-left: rem(28);
      }
    }
  }
  b {
    color: var(--orange);
  }
}

.gap {
  &--top {
    &--xxl  {
      padding-top: rem(100);
      @media (min-width: em(992)) {
        padding-top: rem(200);
      }
    }
    &--xl {
      padding-top: rem(80);
      @media (min-width: em(992)) {
        padding-top: rem(160);
      }
    }
    &--lg {
      padding-top: rem(60);
      @media (min-width: em(992)) {
        padding-top: rem(120);
      }
    }
    &--md {
      padding-top: rem(50);
      @media (min-width: em(992)) {
        padding-top: rem(100);
      }
    }
    &--sm {
      padding-top: rem(40);
      @media (min-width: em(992)) {
        padding-top: rem(80);
      }
    }
    &--sx {
      padding-top: rem(36);
      @media (min-width: em(992)) {
        padding-top: rem(72);
      }
    }
  }
  &--bottom {
    &--xxl  {
      padding-top: rem(100);
      @media (min-width: em(992)) {
        padding-top: rem(200);
      }
    }
    &--xl {
      padding-bottom: rem(80);
      @media (min-width: em(992)) {
        padding-bottom: rem(160);
      }
    }
    &--lg {
      padding-bottom: rem(60);
      @media (min-width: em(992)) {
        padding-bottom: rem(120);
      }
    }
    &--md {
      padding-bottom: rem(50);
      @media (min-width: em(992)) {
        padding-bottom: rem(100);
      }
    }
    &--sm {
      padding-bottom: rem(40);
      @media (min-width: em(992)) {
        padding-bottom: rem(80);
      }
    }
    &--sx {
      padding-bottom: rem(36);
      @media (min-width: em(992)) {
        padding-bottom: rem(72);
      }
    }
  }
}

.pc-item {
  &--sm {
    @media (max-width: em(768)) {
      display: none !important;
    }
  }
  &--md {
    @media (max-width: em(992)) {
      display: none !important;
    }
  }
  &--lg {
    @media (max-width: em(1240)) {
      display: none !important;
    }
  }
}

.mob-item {
  &--sm {
    @media (min-width: em(768)) {
      display: none !important;
    }
  }
  &--md {
    @media (min-width: em(992)) {
      display: none !important;
    }
  }
  &--lg {
    @media (min-width: em(1240)) {
      display: none !important;
    }
  }
}

