.weady-manage-drag-n-drop {
  li {
    list-style: disc;
  }
  .ai-website-builder-class-drag-n-drop {
    height: fit-content;
    margin-top: rem(25);
    .text-nowrap {
      @media (max-width: 992px) {
        font-size: rem(9.05866);
      }
    }
  }
  &__content {
    @media (min-width: 992px) {
      max-width: calc(50% - rem(60));
      margin-right: rem(60);
    }
    &-wrap {
      &.active {
        display: flex;
      }
    }
  }
  .drag-n-drop-item {
    transition: .2s;
    &:hover,
    &.active {
      box-shadow: 0 rem(49) rem(67) rgba(70, 92, 91, .16);
      background-color: var(--green2);
      color: var(--white);
    }
  }
  .tab-nav {

    @media (max-width: 992px) {
      @include scrollbar-x-block;
    }
    &__item {
      font-size: rem(16);
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
  }
}