.help-page {
  h2 {
    font-weight: 900;
    font-size: rem(36) !important;
    line-height: 1.10;
    background: linear-gradient(98.08deg, rgba(80, 128, 104, 0.87) -0.92%, rgba(0, 38, 38, 0.87) 84.18%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;

  }
  .course__content-wrap .btn-ui {
    margin-top: rem(60);
    text-transform: capitalize;
    min-width: rem(307);
    font-size: rem(16);
    height: rem(50);
    @media (min-width: 992px) {;
      min-width: rem(378);
      margin-top: rem(92);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {;
      height: rem(60);
      font-size: rem(22);
    }
  }
}

.weady-for-item-title {
  width: 100% !important;
  @media (min-width: 992px) {
    width: 50% !important;
  }
}
.weady-qa-simple {
  padding: 0 !important;
  .weady-for-item-title {
    width: 100% !important;
    padding-right: rem(48);
    font-size: rem(20) !important;
    @media (min-width: 992px) {
      font-size: rem(24) !important;
    }
  }
  .weady-for-item-data {
    @media (max-width: 992px) {
      width: 100%;
    }
  }
  .weady-for-item-link {
    span {
      @media (max-width: 992px) {
        display: none;
      }
    }
  }
}

.help-inner {
  h1, h2, h3, h4, h5, h6 {
    padding-bottom: rem(10);
  }
  p+p {
    padding-top: rem(20);
  }
}

.help-bottom {
  display: flex;
  justify-content: space-between;
  padding-top: rem(40);
  @media (max-width: 1400px) {
    flex-direction: column;
  }
  &-box {
    padding: rem(52) rem(40);
    border: rem(1) solid #5B8473;
    border-radius: rem(10);
    @media (min-width: 1400px) {
      margin-right: rem(30);
    }
    &__headline {
      padding-bottom: rem(11);
      font-weight: 700;
      line-height: 1.98;
      letter-spacing: rem(.15);
      color: #204242;
      font-size: rem(16);
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &__desc {
      line-height: 1.64;
      letter-spacing: rem(.15);
      color: rgba(76, 78, 100, .6);
      font-size: rem(16);
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &__btns {
      padding-top: rem(20);
      display: flex;
    }
    &__btn {
      width: 100%;
      height: 100%;
      max-width: rem(34);
      max-height: rem(31);
      min-width: rem(34);
      min-height: rem(31);
      @media (min-width: 1600px) {
        max-width: rem(42);
        max-height: rem(38);
        min-width: rem(42);
        min-height: rem(38);
      }
      img {
        @include contain;
      }
      &+.help-bottom-box__btn {
        margin-left: rem(17);
        @media (min-width: 1600px) {
          margin-left: rem(21);
        }
      }
    }
  }
  &-links {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: fit-content;
    @media (min-width: 1400px) {
      max-width: 50%;
    }
    @media (max-width: 1400px) {
      margin-top: rem(63);
    }
    &__desc {
      display: flex;
      justify-content: center;
      padding-bottom: rem(15);
      margin: 0 auto;
      font-weight: 700;
      line-height: 1.98;
      letter-spacing: rem(.15);
      color: #204242;
      text-align: center;
      font-size: rem(16);
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
      @media (max-width: 1400px) {
        padding-top: rem(60);
      }
      a {
        display: inline-flex;
        margin-left: rem(7);
        color: #FD804C;
        text-decoration: none;
        white-space: nowrap;
      }
    }
    //&__bottom {
    //  height: fit-content;
    //  width: 100%;
    //}
    .btn-ui {
      margin-top: 0 !important;
      font-weight: 700;
      font-size: rem(16) !important;
      @media (min-width: 992px) {
        font-size: rem(18) !important;
      }
      @media (min-width: 1600px) {
        font-size: rem(22) !important;
      }
      &--green {
        &:hover {
          border: rem(1) solid var(--green);
          background-color: transparent;
        }
      }
    }
  }
}