.support {
  &-hero {
    &__headline {
      width: 100%;
      max-width: rem(911);
    }
    .btn {
      @media (max-width: 992px) {
        margin: 0 auto;
      }
      &:hover {
        color: var(--white) !important;
      }
    }
  }
  &-question {
    &__content {
      position: relative;
    }
    .ai-website-builder-class-2 {
      width: rem(71);
      height: rem(71);
      font-size: rem(14.0247);
      @media (min-width: 992px) {
        right: rem(8);
        width: rem(60);
        height: rem(60);
        transform: rotate(-90deg);
        font-size: rem(11.8519);
      }
      @media (min-width: 1600px) {
        width: rem(63);
        height: rem(63);
        font-size: rem(16);
      }
    }
    .weady-beta {
      @media (max-width: 992px) {
        display: none !important;;
      }
    }
    .question-label {
      @media (max-width: 992px) {
        padding-top: rem(20);
      }
    }
  }
}

.support-benefits {
  .btn {
    transition: .2s;
    &:hover {
      border: rem(1) solid var(--orange);
    }
  }
}