// NORMALIZE
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none !important;
  list-style: none;

  font-family: var(--fnt_main);
  overflow-wrap: break-word;
  //word-break: break-word;
  text-rendering: optimizeSpeed;

  &::selection {
    background-color: var(--text_selection_bg);
    color: var(--text_selection_clr);
  }
}

html,
body {
  min-width: 320px;
  width: 100%;
  font-size: 100%;
  position: relative;
}

html {
  scroll-behavior: smooth;
  @include scrollbar(var(--scrollBar-radius), var(--scrollBar-color), var(--scrollBar-width), var(--scrollBar-height), $gap: var(--scrollBar-gap));
}

body {
  min-height: 100vh;
}

