mark {
  background-color: transparent;
  color: inherit;
}

a,
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: 1;
  // Убирает мерцание при клике на IOS
  // user-select: none;
  // -webkit-tap-highlight-color: transparent;
}

button,
select {
  text-transform: none;
}

// button,
// [type="button"],
// [type="reset"],
// [type="submit"] {
//     appearance: button;
// }

textarea {
  resize: none;
  overflow: auto;
}

input::-ms-clear {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  margin: 0;
  width: 1em;
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
  }
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

button {
  cursor: pointer;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

iframe {
  border: 0;
}

select {
  border: 0 solid transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;

  &::-ms-expand {
    display: none;
  }
}

blockquote,
q {
  quotes: none;
}

[hidden] {
  display: none;
}

b,
strong {
  font-weight: bolder;
}

progress {
  vertical-align: baseline;
}

[data-bg],
[data-ibg] {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

// NORMALIZE
