.quote {
  &-wrap {
    padding: rem(85) 0;
    text-align: center;
    @media (min-width: 992px) {
      padding: rem(97) 0;
    }
    @media (min-width: 1600px) {
      padding: rem(132) 0;
    }
  }
  &__content {
    width: 100%;
    max-width: rem(1141);
    margin: 0 auto;
    @media (min-width: 1600px) {
      max-width: none;
    }
  }
  &__subtitle {
    font-size: rem(16);
    line-height: 2;
    letter-spacing: rem(.15);
    text-transform: uppercase;
    color: rgba(76, 78, 100, .6);
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(5);
      font-size: rem(22);
    }
  }
  &__headline {
    padding-bottom: rem(70);
    color: var(--green5);
    @media (min-width: 992px) {
      padding-bottom: rem(48);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(125);
    }
    b {
      color: var(--orange);
    //  background: linear-gradient(100deg,var(--orange) 100%,var(--orange) 100%);
    //  -webkit-background-clip: text;
    //  -webkit-text-fill-color: transparent;
    //  background-clip: text;
    //  text-fill-color: transparent;
      //color: rgba(253, 128, 76, 0.87);
    }
  }
  &__desc {
    position: relative;
    width: fit-content;
    margin: 0 auto;
    font-size: rem(12.0296);
    line-height: 2;
    letter-spacing: rem(.215624);
    color: var(--green);
    @media (min-width: 992px) {
      font-size: rem(17.25);
    }
    @media (min-width: 1600px) {
      font-size: rem(23.8845);
    }

    &:before {
      content: '';
      position: absolute;
      top: rem(-15);
      right: calc(50% - rem(26.5));
      z-index: -1;
      width: rem(53);
      height: rem(53);
      min-width: rem(53);
      min-height: rem(53);
      border-radius: 50%;
      background-color: var(--orange);
      @media (min-width: 992px) {
        top: rem(-23);
        right: calc(50% - rem(38));
        width: rem(76);
        height: rem(76);
        min-width: rem(76);
        min-height: rem(76);
      }
      @media (min-width: 1600px) {
        top: rem(-28);
        right: calc(50% - rem(52.5));
        width: rem(105);
        height: rem(105);
        min-width: rem(105);
        min-height: rem(105);
      }
    }
  }
}