.program {
  display: flex;
  justify-content: space-between;
  @media (max-width: 992px) {
    flex-direction: column;
  }
  .btn {
    min-width: rem(216);
    @media (min-width: 992px) {
      min-width: rem(281);
    }
    &:hover {
      color: var(--white) !important;
    }
  }
  &-wrap {
    margin-top: rem(-252);
    padding-top: rem(236.5);
    padding-bottom: rem(124);
    @media (min-width: 992px) {
      margin-top: rem(-346);
      padding-top: rem(360);
      padding-bottom: rem(64);
    }
    @media (min-width: 1600px) {
      margin-top: rem(-416);
      padding-top: rem(396);
      padding-bottom: rem(101);

      //padding-bottom: rem(119);
    }
  }
  &__desc-main {
    height: fit-content;
    @media (min-width: 992px) {
      min-height: rem(744);
      padding-right: rem(70);
    }
    @media (min-width: 1600px) {
      min-height: rem(825);
    }
    &-text {
      @media (max-width: 992px) {
        margin-bottom: rem(79) !important;
      }
    }
  }
  &__head-desc {
    padding-top: rem(15);
    padding-bottom: rem(15);
    font-size: rem(16);
    @media (min-width: 992px) {
      padding-top: rem(25);
      padding-bottom: rem(35);
    }
    @media (min-width: 1200px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(50);
      font-size: rem(22);
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    @media (min-width: 992px) {
      margin-top: rem(-119);
    }
    @media (min-width: 992px) and (max-width: 1600px) {
      width: 50%;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    width: 100%;
    @media (min-width: 992px) {
      justify-content: flex-end;
    }
    &:not(:last-child) {
      margin-bottom: rem(22);
      @media (min-width: 992px) {
        margin-bottom: rem(19.5);
      }
      @media (min-width: 1600px) {
        margin-bottom: rem(26);
      }
    }
  }
  &__count {
    min-width: rem(122);
    margin-right: rem(14);
    font-weight: 700;
    font-size: rem(89);
    line-height: .98;
    color: var(--white);
    @media (max-width: 768px) {
      display: none;
    }
  }
  &__desc {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: rem(189);
    max-width: rem(483);
    padding: rem(36) rem(37) rem(47);
    border-radius: rem(15);
    background-color: var(--white);
    box-shadow: 0 rem(44) rem(91) rgba(0, 0, 0, .22);
    @media (min-width: 768px) {
      max-height: rem(189);
    }
    @media (min-width: 992px) {
      max-height: rem(168);
      min-height: rem(168);
      padding: rem(15) rem(32) rem(15) rem(46);
    }
    @media (min-width: 1200px) {
      width: 100%;
      min-width: rem(483);
    }
    @media (min-width: 1600px) {
      max-height: rem(223);
      min-height: rem(223);
      max-width: rem(487);
      min-width: rem(487);
    }
  }
  &__headline {
    font-size: rem(42);
    @media (max-width: 992px) {
      padding-top: rem(90);
    }
    @media (min-width: 1200px) {
      font-size: rem(64);
    }
    @media (min-width: 1600px) {
      font-size: rem(76);
    }
  }
  &__title {
    font-weight: 700;
    font-size: rem(26);
    line-height: 1.09;
    letter-spacing: .218775px;
    color: var(--dark);
    @media (min-width: 992px) {
      font-size: rem(24);
    }
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
  }
  &__text {
    padding-top: rem(10);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: .110124px;
    color: var(--dark);
    @media (min-width: 1200px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
}