.pack {
  &-wrap {
    @media (min-width: em(1200)) {
      padding-top: rem(70);
    }
    &+.pack-wrap {
      @media (max-width: em(1200)) {
        padding-top: rem(70);
      }
    }
  }
  &__headline {
    width: 100%;
    max-width: rem(1010);
    line-height: 1.2;
    @media (max-width: em(1200)) {
      //max-width: rem(620);
      font-size: rem(42);
    }
    @media (max-width: 768px) {
      font-size: 2.2rem;
    }
    br {
      @media (max-width: em(768)) {
        display: none;
      }
    }
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &-item {
      position: relative;
      margin-top: rem(45);
      color: #254746;
      font-size: rem(11);
      line-height: 2;
      letter-spacing: rem(.216);
      transform: rotate(90deg);
      white-space: nowrap;
      @media (min-width: em(992)) {
        font-size: rem(17.25);
      }
      @media (max-width: em(1200)) {
        display: none;
      }
      &:before {
        content: '';
        position: absolute;
        right: rem(-22);
        top: rem(-11);
        z-index: -1;
        max-width: rem(48);
        max-height: rem(48);
        min-width: rem(48);
        min-height: rem(48);
        border-radius: 50%;
        background-color: #FD804C;
        @media (min-width: em(992)) {
          top: rem(-22);
          max-width: rem(76);
          max-height: rem(76);
          min-width: rem(76);
          min-height: rem(76);
        }
      }
    }
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: rem(40);
    @media (max-width: 992px) {
      flex-direction: column;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin: rem(23) 0;
    border-radius: rem(15);
    overflow: hidden;
    border: rem(1) solid #D4D4D4;
    box-shadow: 0 rem(44) rem(91) 0 rgba(0, 0, 0, .05);
    @media (min-width: 992px) {
      max-width: calc(50% - rem(20));
    }
    &-content {
      padding: rem(30);
      padding-left: 2rem;
      color: #7B7B7B;
      font-size: rem(16);
      line-height: 1.6;
      letter-spacing: rem(.15);
      @media (min-width: 992px) {
        padding: rem(52) rem(55);
        padding-left: 2rem;
      }
      @media (min-width: 1200px) {
        font-size: rem(20);
        padding-left: rem(55);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
  }
  &__title {
    color: var(--unnamed, #4E7863);
    font-size: rem(24);
    font-weight: 900;
    line-height: 1.1;
    @media (min-width: 1200px) {
      font-size: 2rem;
    }
    @media (min-width: 1600px) {
      font-size: rem(42);
    }
  }
  &__desc {
    padding: rem(30) 0 rem(24.5);
    @media (min-width: 1200px) {
      padding-bottom: rem(35);
    }
  }
  &__li {
    display: flex;
    &+.pack__li {
      padding-top: rem(10);
    }
    &-ico {
      max-width: 1.3rem;
      max-height: 1.3rem;
      min-width: 1.3rem;
      min-height: 1.3rem;
      @include contain;
      margin-right: rem(9);
      margin-top: rem(5);
      @media (min-width: 1200px) {
        max-width: rem(26);
        max-height: rem(26);
        min-width: rem(26);
        min-height: rem(26);
      }
    }
    &-text {

    }
  }
  &__btn {
    display: flex;
    align-items: center;
    margin-top: rem(25);
    text-decoration: none;
    font-size: rem(16);
    @media (min-width: 1200px) {
      font-size: rem(20);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
    &:hover {
      color: white;
    }
    &-ico {
      max-width: rem(26);
      max-height: rem(26);
      min-width: rem(26);
      min-height: rem(26);
      @include contain;
      margin-right: rem(11);
      @media (min-width: 992px) {
        max-width: rem(34);
        max-height: rem(34);
        min-width: rem(34);
        min-height: rem(34);
      }
    }
    &-text {
      color: #7B7B7B;
      line-height: 1;
    }
    &--link {
    }
    &--orange {
      width: fit-content;
      min-height: rem(35);
      padding: 0 rem(15);
      justify-content: center;
      border-radius: rem(10);
      background-color: #FD804C;

      color: #FFF;
      text-align: center;
      line-height: 1;
      @media (min-width: 1200px) {
        min-height: rem(50);
        padding: 0 rem(32);
      }
    }
  }
  &__img {
    max-height: rem(217);
    max-width: 100%;
    object-fit: contain;
  }
}