.pagination {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: rem(458);
  margin: 0 auto;
  &-wrap {
    padding-top: rem(30);
    padding-bottom: rem(90);
    @media (max-width: 992px) {
      display: none !important;
    }
  }
  &__btn {
    font-size: rem(22);
    line-height: 1.64;
    letter-spacing: rem(.110124);
    color: #204242;
    text-decoration: none;
    &:hover {
      color: #204242;
    }
  }
  &__content {
    display: flex;
    align-items: center;
    margin: 0 rem(11);
    padding: 0 rem(35);
  }

  &__dots,
  &__item {
    font-weight: 700;
    font-size: rem(22);
    line-height: 1.64;
    text-align: center;
    letter-spacing: rem(.110124);
    color: #204242;
    text-decoration: none;
    &:hover {
      color: #204242;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 rem(15);
  }
  &__dots {
  }
}