.benefits {
  &-wrap {
    padding-bottom: rem(35);
    @media (min-width: 1600px) {
      padding-bottom: rem(61);
    }
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 992px) {
      flex-direction: column;
    }
  }
  &__col {
    width: 100%;
    @media (min-width: 992px) {
      max-width: calc(50% - rem(24));
    }
    @media (min-width: 1600px) {
      max-width: calc(50% - rem(26));
    }
    &:nth-child(2n + 1) {
      .benefits__item {
        &:first-child {
          background-color: var(--green2);
          @media (min-width: 992px) {
            height: 100%;
            max-height: calc(55% - rem(18));
            min-height: calc(55% - rem(18));
          }
          @media (min-width: 1600px) {
            max-height: calc(55% - rem(21.5));
            min-height: calc(55% - rem(21.5));
          }
          .benefits__title {
            color: var(--white);
          }
          li {
            color: var(--grey3);
          }
        }
        &:nth-child(2) {
          background-color: var(--green-light2);
          @media (min-width: 992px) {
            height: 100%;
            max-height: calc(45% - rem(18));
            min-height: calc(45% - rem(18));
          }
          @media (min-width: 1600px) {
            max-height: calc(45% - rem(21.5));
            min-height: calc(45% - rem(21.5));
          }
          .benefits__title {
            color: var(--green2);
          }
          li {
            color: var(--grey2);
          }
        }
      }
    }
    &:nth-child(2n + 2) {
      .benefits__item {
        &:first-child {
          background-color: var(--green-light2);
          @media (min-width: 992px) {
            height: 100%;
            max-height: calc(45% - rem(18));
            min-height: calc(45% - rem(18));
          }
          @media (min-width: 1600px) {
            max-height: calc(45% - rem(21.5));
            min-height: calc(45% - rem(21.5));
          }
          .benefits__title {
            color: var(--green2);
          }
          li {
            color: var(--grey2);
          }
        }
        &:nth-child(2) {
          background-color: var(--green2);
          @media (min-width: 992px) {
            height: 100%;
            max-height: calc(55% - rem(18));
            min-height: calc(55% - rem(18));
          }
          @media (min-width: 1600px) {
            max-height: calc(55% - rem(21.5));
            min-height: calc(55% - rem(21.5));
          }
          .benefits__title {
            color: var(--white);
          }
          li {
            color: var(--grey3);
          }
        }
      }
    }
  }
  &__item {
    width: 100%;
    padding: rem(48) rem(26) rem(47) rem(60);
    margin-bottom: rem(36);
    border-radius: rem( 9.11681);
    @media (min-width: 1600px) {
      padding: rem(53) rem(106) rem(53) rem(66);
      margin-bottom: rem(43);
    }
  }
  &__title {
    padding-bottom: rem(30);
    font-weight: 900;
    font-size: rem(24);
    line-height: 1.35;
    letter-spacing: rem(.136752);
    color: var(--white);
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
  }
  li {
    padding-left: rem(12);
    font-size: rem(18);
    line-height: 1.47;
    letter-spacing: rem(.136752);
    list-style-image: url("../img/ico/Check.svg");
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
}