.main-new-page {
  .how-it-work-block__img {
    @media (min-width: 992px) {
      min-width: auto !important;
      max-height: none;
      min-height: 100%;
      width: fit-content !important;
    }
  }
}
.how-it-work-block {
  &__img {
    border-radius: rem(15);
    overflow: hidden;
    @media (min-width: 992px) {
      position: absolute;
      right: 0;
      max-width: rem(1299);
      max-height: rem(704);
      //min-width: rem(1299);
      //min-height: rem(704);
      min-height: 100%;
      @include contain;
      width: 100% !important;
    }
    @media (min-width: 1440px) {
      min-width: rem(1299);
    }
    &-wrap {
      position: relative;
    }
  }
}

.steps {
  &__headline {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  &__desc {
    width: 100%;
    max-width: rem(430);
    color: #6E8E8E;
    font-size: rem(18);
    line-height: 1.6;
    letter-spacing: rem(.15);
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
    @media (min-width: 992px) {
      padding-top: rem(10);
    }
  }
  h1, h2, h3, h4, h5, h6 {
    @media (max-width: 768px) {
      font-size: 2.2rem;
    }
  }
}

.steps-tabs {
  padding: rem(35) 0;
  @media (min-width: 992px) {
    padding: rem(100) 0;
  }
  .how-it-work {
    &-items {
      margin-top: 0;
      @media (max-width: 1200px) {
        width: 45%;
      }
    }
    &-item {
      opacity: 1;
      margin-bottom: rem(20) !important;
      &-description {
        max-height: rem(140);
        padding-left: rem(48) !important;
        background-color: #CDDED6;
        color: #2B4D4A;
        border-radius: rem(13);
        border: rem(1) solid #D1D1D1;
        box-shadow: 0 3px 30px 0 rgba(0, 0, 0, .10) !important;
        @media (max-width: 1200px) {
          font-size: rem(16);
        }
      }
      &-number {
        color: #CDDED6;
        opacity: .5;
      }
      &-wrap {
        &.active {
          .how-it-work-item {
            &-description {
              background: linear-gradient(0deg, #FD804C 0%, #FD804C 100%), #FD804C;
              color: white;
              border: rem(1) solid #FD804C;
            }
            &-number {
              color: #FD804C;
              opacity: 1;
            }
          }
        }
      }
    }
    &-block-slider {
      margin-top: 0 !important;
    }
    &-block__img-wrap {
      @media (max-width: 1200px) {
        width: 55%;
      }
    }
  }
  .steps {
    height: auto;
    &__headline {
      position: relative;
      top: 0;
      align-items: flex-start;
      padding-bottom: rem(40);
      @media (max-width: 992px) {
        flex-direction: column;
      }
      h1, h2, h3, h4, h5, h6 {
        margin-top: 0 !important;
      }
    }
    &__content-wrap {
      position: relative;
      height: auto;
      padding: 0;
      flex-direction: column;
    }
  }
}

.how-it-work-block__img-inner  {
  transition: .2s;
  opacity: 0;
  &.active {
    opacity: 1;
  }
}