.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  &-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    position: fixed;
    top: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
  }
  &__gif {
    max-width: rem(70);
    max-height: rem(70);
    @include contain;
    //@media (min-width: 992px) {
    //  max-width: rem(182);
    //  max-height: rem(182);
    //}
  }
  &__img {
    max-width: rem(130);
    max-height: rem(130);
    margin-top: rem(56);
    @include contain;
    //@media (min-width: 992px) {
    //  max-width: rem(349);
    //  max-height: rem(92);
    //}
  }
}