.reviews-page {
  .review__content-wrap {
    padding-bottom: 0;
  }
}
.partner-page {
  .review {
    .mCSB_draggerContainer {
      height: rem(1) !important;
      background-color: var(--dark) !important;
    }

    .mCSB_dragger_bar {
      //height: rem(1);
      top: rem(-16) !important;
      //top: rem(0) !important;
      //margin: rem(0) !important;
      background-color: var(--dark) !important;
      border-radius: 50%;
      //height: rem(33) !important;
      //width: rem(33) !important;
      height: rem(24) !important;
      transition: none !important;
      @media (min-width: 992px) {
        //top: rem(-24) !important;
        //width: rem(48) !important;
      }
    }

    .mCSB_draggerRail {
      background-color: transparent !important;
    }

    //.mCSB_dragger,
    .mCSB_scrollTools_horizontal {
      //margin-bottom: rem(-20) !important;
      max-width: rem(195) !important;
      bottom: rem(-120) !important;
      opacity: 1 !important;
      @media (min-width: 992px) {
        max-width: rem(284) !important;
        bottom: rem(-94) !important;
      }
    }

    .mCustomScrollBox {
      overflow: visible;
    }
  }
}

.course-page {
  .mCSB_draggerRail {
    background-color: transparent !important;
  }
  .mCSB_draggerContainer {
    width: rem(1);
    background-color: rgba(0,0,0,.15) !important;
  }
  .mCSB_dragger_bar {
    margin-left: rem(-1) !important;
  }
}

.reviews-page,
.time,
.team {
  .mCSB_container {
    display: flex;
  }
  .mCSB_dragger_bar {
    background-color: transparent !important;
  }
}

