@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;

    //src: url('../fonts/Inter/static/#{$file_name}.eot'); /* IE9 Compat Modes */

    src:
        //local('#{$file_name}'),
        //url('../fonts/#{$file_name}.eot?#iefix') format('embedded-opentype'), //Старі версії IE. IE6-IE8
        //url("../fonts/#{$file_name}.otf") format("opentype"),                 //за ідеєю, важить менше.
        //url('../fonts/#{$file_name}.ttf') format('truetype'),                 //Старі Safari, Android, iOS(*)
        //url("../fonts/#{$file_name}.woff2") format("woff2"),                  //Найновіші браузери
        url("../fonts/Inter/static/#{$file_name}.woff") format("woff");                    //Нові браузери та IE9+
        //url('../fonts/#{$file_name}.svg#slick') format('svg');                //Стародавні Safari, iOS, Android
        font-weight: #{$weight};
        font-style: #{$style};
        // unicode-range: U+0410-044F; //тільки кирилиця
  }
}

@import "./_font-list.scss";
