.blog {
  &-hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: var(--white);
    &-wrap {
      padding: rem(76) 0 rem(98);
      background: linear-gradient(233.22deg, rgba(80, 118, 100, 0.7) -39.68%, rgba(120, 173, 148, 0.7) -23.23%, rgba(59, 99, 80, 0.7) -8.1%, rgba(70, 112, 92, 0.7) 7.69%, rgba(47, 85, 67, 0.7) 22.83%, rgba(89, 126, 109, 0.7) 37.96%, rgba(128, 174, 153, 0.7) 53.75%, rgba(80, 114, 98, 0.7) 70.2%, rgba(67, 95, 82, 0.7) 86.65%), #204242;
      @media (min-width: 992px) {
        padding: rem(77) 0 rem(125);
      }
      @media (min-width: 1600px) {
        padding: rem(63) 0 rem(93);
      }
    }
    &__content {
      width: 100%;
      max-width: rem(678);
      @media (min-width: 1600px) {
        max-width: rem(863);
      }
    }
    &__headline {
      color: var(--white);
    }
    &__desc {
      width: 100%;
      padding: rem(16) 0 rem(45);
      font-size: rem(16);
      line-height: 1.64;
      letter-spacing: rem(.110124);
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
    }
    &__search {
      position: relative;
    }
    &__field {
      height: rem(48);
      width: 100%;
      padding: 0 rem(19);
      background-color: #CDDED6;
      border-radius: rem(10);
      @media (min-width: 1600px) {
        height: rem(60);
      }
      &::placeholder {
        font-size: rem(18);
        line-height: 1.85;
        letter-spacing: rem(.229861);
        color: #204242;
        opacity: .6;
        @media (min-width: 1600px) {
          font-size: rem(22);
        }
      }
      &.active {
        border-radius: rem(10) rem(10) 0 0;
      }
      &-drop {
        position: absolute;
        top: rem(48);
        width: 100%;
        border-radius: 0 0 rem(10) rem(10);
        overflow: hidden;
        @media (min-width: 1600px) {
          top: rem(60);
        }
        &-item {
          height: rem(48);
          width: 100%;
          background-color: #F3F3F3;
          @media (min-width: 1600px) {
            height: rem(60);
          }
        }
      }
    }
    &__labels {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: rem(13);
      @media (min-width: 1600px) {
        padding-top: rem(16);
      }
      @media (max-width: 992px) {
        flex-wrap: wrap;
      }
    }
    &__label {
      font-size: rem(16);
      line-height: 1.64;
      letter-spacing: rem(.110124);
      text-decoration: none;
      color: var(--white);
      @media (max-width: 992px) {
        width: 50%;
        padding: rem(17);
      }
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
      &:hover{
        color: var(--white);
      }
      &.active {
        font-weight: 700;
        color: var(--white);
      }
    }
  }
}