.docs {
  &-page {
    .weady-start-create-site-block-header {
      margin: 0 auto !important;
    }
    .weady-start-create-site-block-description-docs {
      max-width: rem(597);
      margin-left: auto !important;
      margin-right: auto !important;
      @media (min-width: 1600px) {
        max-width: rem(931);
      }
    }
    .links-block {
      margin-bottom: rem(12) !important;
      p {
        font-weight: 400;
        font-size: rem(16);
        @media (max-width: 992px) {
          font-size: rem(18);
        }
        @media (max-width: 1600px) {
          font-size: rem(22);
        }
        &:first-child {
          font-weight: 700;
          font-size: rem(16);
          @media (max-width: 992px) {
            font-size: rem(20);
          }
          @media (max-width: 1600px) {
            font-size: rem(26);
          }
        }
      }
    }
    .weady-join-beta {
      margin-top: 0 !important;
      .container {
        margin-top: 0 !important;
      }
    }
    .course {
      &__nav-wrap {
        max-width: rem(409);
        &::before {
          background-color: #cdded6;
        }
      }
      &-nav-mob {
        &__content {
          background-color: #CDDED6;
        }
        &-wrap {
          background-color: #CDDED6;
        }
      }
    }
  }
}