:root {
  // FONTS
  // =====================
  --fnt_main: 'Inter', sans-serif;
  --fnt_sec: 'Inter', sans-serif;
  // ===========================
  // FONTS


  // COLORS THEMES
  // ========================
  @include color_theme($color_scheme_main);
  --white: #fff;
  --black: #000;
  --black2: #4E4E4E;
  --dark: #204242;
  --dark2: #313131;
  --dark3: #345852;
  --orange: #FD804C;
  --orange2: #FB6D33;
  --grey: #EAEDED;
  --grey2: #7B7B7B;
  --grey3: #D5D7D6;
  --green: #254746;
  --green2: #2B4D4A;
  --green3: #00B67A;
  --green4: #4E7863;
  --green5: #2C4F4B;
  --green6: #224343;
  --green7: #508068;
  --green-dark: #313C36;
  --green-light: #F0F4F2;
  --green-light2: #CDDED6;

  // .theme_sec {
  //   $color_scheme_sec: ('prim': #FFD112,
  //       'sec': #CD2D2B,
  //     );
  //   @include color_theme($color_scheme_sec);
  // }

  // ==========================
  // COLORS THEMES



  // COLORS DEPENDENSIS
  // ====================
  // scrollBar
  --scrollBar-color: #{clr(prim, 1)};

  //--scrollBar-color: var(--grad_prim);
  --scrollBar-width: 8px;
  --scrollBar-height: auto;
  --scrollBar-radius: 0px;
  --scrollBar-gap: 0px;

  // Text selection color
  --text_selection_bg: #{clr(prim, 1)};
  --text_selection_clr: #{clr(light, 1)};

  // Select scroll color
  --select-scroll: #{clr(divider, 1)};
  // =================
  // COLORS DEPENDENSIS
}


