.time {
  &-wrap {
    padding: rem(53) 0;
    background: linear-gradient(0deg, #FD804C, #FD804C), #5B8473;
    color: var(--white);
    @media (min-width: 992px) {
      padding: rem(95) 0 rem(92);
    }
    @media (min-width: 1600px) {
      padding: rem(134) 0 rem(149);
    }
  }
  &__headline {
    line-height: 1.1;
    color: var(--white);
    @media (min-width: 992px) {
      text-align: center;
    }
  }
  &__content {
    display: flex;
    padding-top: rem(32);
    @media (min-width: 992px) {
      padding-top: rem(65);
    }
    @media (min-width: 1600px) {
      padding-top: rem(85);
    }
  }
  &__item {
    width: 100%;
    max-width: calc(100% - rem(59));
    min-width: calc(100% - rem(59));
    padding-top: rem(90);
    &-wrap {
      position: relative;
      width: 100%;
      max-width: rem(500);
      min-width: rem(500);
      @media (min-width: 992px) {
        max-width: rem(517);
        min-width: rem(517);
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: rem(45);
        height: rem(45);
        max-width: rem(45);
        max-height: rem(45);
        min-width: rem(45);
        min-height: rem(45);
        background-color: var(--orange);
        border: rem(3) solid var(--white);
        border-radius: 50%;
        @media (min-width: 992px) {
          width: rem(56);
          height: rem(56);
          max-width: rem(56);
          max-height: rem(56);
          min-width: rem(56);
          min-height: rem(56);
        }
      }
      &:before {
        content: '';
        position: absolute;
        top: rem(22);
        left: 0;
        width: 100%;
        height: rem(1);
        border-top: rem(1) dashed var(--white);
        @media (min-width: 992px) {
          top: rem(30);
        }
      }
      &:first-child,
      &:nth-child(2) {
        &:after {
          background-color: var(--white);
        }
      }
    }
  }
  &__title {
    font-size: rem(20);
    line-height: .6;
    letter-spacing: rem(.15);
    @media (min-width: 992px) {
      font-size: rem(24);
    }
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
    b {
      font-weight: 900;
    }
  }
  &__desc {
    padding-top: rem(12.91);
    font-size: rem(16);
    line-height: 1.47;
    letter-spacing: rem(.15);
    color: var(--white);
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
}