.bunner-tokens {
  display: flex;
  justify-content: space-between;
  @media (max-width: 992px) {
    flex-direction: column;
  }
  &-wrap {

  }
  &__box {
    height: 100%;
    padding: rem(48) rem(35) rem(48) rem(36);
    border-radius: rem(10);
    @media (min-width: 1200px) {
      padding: rem(62) rem(63) rem(55) rem(57);
    }
    &--green-light {
      background-color: #F0F4F2;
      .bunner-tokens {
        &__headline {
          color: #435F52;
        }
        &__label {
          color: #333333;
          &:before {
            background-color: var(--orange);
          }
        }
      }
    }
    &--green {
      background-color: #5D8674;
      .bunner-tokens {
        &__headline {
          color: #F8F8F8;
        }
        &__desc {
          color: #FAFAFA;
        }
        &__label {
          color: var(--white);
          &:before {
            background-color: var(--white);
          }
        }
      }
    }
    &--top {
      max-height: calc(60% - rem(25));
    }
    &--bottom {
      max-height: calc(40% - rem(25));
    }
  }
  &-col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    @media (min-width: 992px) {
      max-width: calc(50% - rem(29));
    }
    &:nth-child(2n) {
      .bunner-tokens__box {
        @media (max-width: 992px) {
          margin-top: rem(31);
        }
      }
    }
  }
  &__headline {
    font-weight: 900;
    font-size: rem(26);
    line-height: 1.1;
    letter-spacing: rem(.15);
    @media (min-width: 1200px) {
      font-size: rem(32);
    }
    &-wrap {
      display: flex;
      align-items: center;
    }
    &-ico {
      max-width: rem(28);
      max-height: rem(28);
      min-width: rem(28);
      min-height: rem(28);
      @include contain;
      margin-left: rem(10);
    }
  }
  &__desc {
    max-width: rem(470);
    padding-top: rem(17);
    font-weight: 400;
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    @media (min-width: 1200px) {
      padding-top: rem(23);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
    b {
      font-weight: 700;
    }
  }
  &__label {
    padding-top: rem(26);
    font-weight: 700;
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    @media (min-width: 1200px) {
      font-size: rem(22);
    }
    &:before {
      content: '';
      display: inline-block;
      margin-right: rem(14.5);
      width: 100%;
      height: 100%;
      max-width: rem(20);
      max-height: rem(20);
      min-width: rem(20);
      min-height: rem(20);
      border-radius: 50%;
      @media (min-width: 1200px) {
        max-width: rem(26);
        max-height: rem(26);
        min-width: rem(26);
        min-height: rem(26);
      }
    }
  }
  &__content {
    ul {
      max-width: rem(553);
      margin: rem(17) 0 rem(29);
      padding: rem(21) rem(19) rem(21) rem(16);
      font-weight: 700;
      font-size: rem(16);
      line-height: 1.64;
      letter-spacing: rem(.15);
      color: #333333;
      border: rem(1) solid #5D8674;
      border-radius: rem(10);
      @media (min-width: 1200px) {
        margin: rem(24) 0 rem(41);
        padding: rem(21) rem(32) rem(21) rem(27);
        font-size: rem(22);
      }
    }
    li {
      display: flex;
      justify-content: space-between;
      b {
        color: #FD804C;
        font-weight: 700;
      }
    }
  }
  &__btn {
    width: 100%;
    max-width: rem(553);
    @media (min-width: 992px) {
      min-width: rem(335);
      width: auto;
    }
    @media (max-width: 992px) {
      height: rem(50);
    }
    &-link {
      display: flex;
      align-items: center;
      font-size: rem(16);
      line-height: 2.25;
      letter-spacing: rem(.15);
      color: var(--white);
      text-decoration: none;
      @media (max-width: 992px) {
        display: none;
      }
      &:hover {
        color: var(--white);
      }
      &-ico {
        max-width: rem(60);
        max-height: rem(60);
        min-width: rem(60);
        min-height: rem(60);
        @include contain;
      }
    }
  }
}