.weady-start-create-site-block-header {
  width: 100%;
  max-width: rem(965);
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 992px) {
    font-size: rem(32);
  }
}

.pre-head-text-demo {
  @media (max-width: 992px) {
    padding-top: rem(40);
    font-size: rem(18);
  }
}


.pre-header-demo {
  @media (max-width: 992px) {
    font-size: rem(26);
  }
}

.demo {
  &-hero {
    .btn {
      margin-left: auto;
      margin-right: auto;
      @media (max-width: 992px) {
        font-size: rem(16);
      }
      &:hover {
        border: rem(1) solid var(--orange)
      }
    }
  }
  &-info {
    &__mark {
      padding-left: rem(23);
    }
    &__subtitle {
      @media (max-width: 992px) {
        font-size: rem(26);
      }
    }
    &__bg-elem {
      @media (max-width: 992px) {
        justify-content: center !important;
        font-size: rem(13.5256);
      }
      &-inner {
        @media (max-width: 992px) {
          margin-left: rem(-30);
        }
      }
      .circle-orange-middle {
        @media (max-width: 992px) {
          max-height: rem(49);
          max-width: rem(49);
          margin: 0 rem(0) 0 rem(-85);
        }
      }
    }
    li {
      @media (max-width: 992px) {
        font-size: rem(22);
      }
    }
    .btn {
      @media (max-width: 992px) {
        font-size: rem(16);
      }
    }
  }
  &-video {
    &-wrap {
      position: relative;
      cursor: pointer;
      max-width: rem(828);
      max-height: rem(504);
      margin: 0 auto;
    }
    &-poster {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      max-width: 100%;
      max-height: 100%;
      @include cover;
    }
    &-btn {
      position: absolute;
      top: calc(50% - rem(39));
      left: calc(50% - rem(39));
      z-index: 1;
      max-width: rem(77);
      max-height: rem(77);
      @include contain;
      @media (min-width: 992px) {
        max-width: rem(132);
        max-height: rem(132);
        top: calc(50% - rem(66));
        left: calc(50% - rem(66));
      }
    }
  }
  &-items {
    padding-left: 0;
  }
}