.course {
  //flex-wrap: nowrap;
  //width: 100%;
  //justify-content: center;
  //overflow: hidden;
  &-page {
    .container,
    .weady-mt {
      margin-top: 0 !important;
    }
    .weady-start-create-site-block-header {
      margin: 0 auto !important;
    }
  }
  &-wrap {
    padding: 0;
  }
  &-hero {
    position: relative;
  }
  &__nav {
    width: 100%;
    //overflow: hidden;
    max-height: calc(100vh - rem(85) - rem(40) - rem(48) - rem(48));
    &-wrap {
      position: sticky;
      top: 0;
      left: 0;
      max-height: 100vh;
      width: 100%;
      max-width: rem(405);
      padding: rem(85) rem(52) rem(48) 0;
      //max-width: calc(rem(1920) * 100 / rem(670));
      //max-width: calc(670 * 100% / 1920);
    //  display: flex;
    //  justify-content: flex-end;
    //  padding: rem(85) rem(52) 0 0;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
        background-color: #fff3ee;
        //background-color: red;
        width: 50vw;
        min-width: 50vw;
        height: 100%;
      }
    }
  }
  &__content {
    width: 100%;
    &-wrap {
      width: 100%;
      max-width: rem(990);
      padding: rem(35) 0 rem(48);
      //max-width: calc(rem(1920) * 100 / rem(990));
      //max-width: calc(990 * 100% / 1920);
      @media (min-width: 992px) {
        padding: rem(85) 0 rem(87) rem(88);
      }
    }
    &-main-wrap {
      margin: 0 auto;
      @media (min-width: 992px) {
        display: flex;
        justify-content: flex-end;
      }
    }
    .white-button {
      color: #5D8674;
      border-color: #5D8674;
      &:hover {
        color: #5D8674;
      }
    }
    .orange-button {
      &:hover {
        border: rem(1) solid var(--orange);
      }
    }
    .btn {
      @media (max-width: 992px) {
        min-width: auto;
        font-size: rem(16);
      }
      @media (max-width: 1200px) {
        width: 100%;
        max-width: calc(50% - rem(5.5));
        padding: 0 !important;
      }
      @media (max-width: 1600px) {
        font-size: rem(18) !important;
      }
    }
  }
  &__btns {
    @media (max-width: 992px) {
      flex-direction: row !important;
    }
  }
  &-nav-mob {
    position: relative;
    &__content {
      visibility: visible;
      position: absolute;
      height: fit-content;
      &.active {
        transform: translateY(0) !important;
      }
      &-body {
        padding: 0 rem(48) rem(125) rem(27);
        .links-block {
          margin-bottom: rem(10) !important;
          cursor: pointer;
          a {
            margin-bottom: rem(5);
          }
        }
      }
      &-inner {
        max-height: rem(458);
      }
    }
    &__items {
      display: flex;
      flex-direction: column;
      padding-top: rem(20);
      a:first-child {
        font-weight: 700;
      }

      a:first-child:before {
        content: "\25B6";
        color: #FD804C;
        position: absolute;
        left: 0;
      }
    }
    &-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      height: rem(59);
      width: rem(59);
      //margin-left: rem(-12);
      padding: 0;
    }
  }
  .navbar-toggler-icon {
    height: rem(25);
    width: rem(32);
  }
  .p-standart-weady {
    @media (max-width: 992px) {
      text-align: left;
    }
  }
  &__quote {
    @media (max-width: 992px) {
      padding: rem(38)  rem(25) !important;
    }
  }
  .links-block p {
    @media (max-width: 992px) {
      font-size: rem(18);
      margin-bottom: 0;
    }
  }
}