.free {
  position: relative;
  &-wrap {
    color: var(--white);
    background-color: var(--orange);
  }
  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    //align-items: flex-end;
    @media (max-width: 992px) {
      flex-direction: column;
    }
  }
  &__desc {
    width: 100%;
    //max-width: rem(501);
    max-width: rem(449);
    @media (max-width: 992px) {
    }
    @media (min-width: 1600px) {
      max-width: rem(686);
    }
    &-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      //height: 100%;
      //padding: rem(85) 0 rem(76);
      @media (max-width: 992px) {
        flex-direction: column;
        align-items: center;
        text-align: center;
      }
      @media (max-width: 992px) {
        padding: rem(64) 0 rem(30);
      }
    }
  }
  &__headline {
    padding-bottom: rem(18);
    font-weight: 700;
    font-size: rem(28);
    line-height: 1.2;
    letter-spacing: rem(.257895);
    color: var(--white);
    @media (min-width: 992px) {
      padding-bottom: rem(2.98);
      font-size: rem(44.7018);
      line-height: 1.1;
    }
    @media (min-width: 1600px) {
      line-height: 1.5;
    }
  }
  &__img {
    position: relative;
    max-width: rem(548);
    max-height: rem(315);
    @include contain;
    @media (min-width: 992px) {
      max-width: rem(544);
      max-height: rem(360);
      margin-right: rem(65);
    }
    @media (min-width: 1600px) {
      max-width: rem(614);
      max-height: rem(406);
      margin-right: rem(129);
    }
    &-wrap {
      position: relative;
      @media (max-width: 992px) {
        margin-top: rem(11);
      }
      &:before {
        content: '';
        position: absolute;
        top: rem(-11);
        left: rem(128);
        width: rem(231);
        height: rem(231);
        background-color: var(--white);
        border-radius: 50%;
        @media (min-width: 992px) {
          width: rem(298);
          height: rem(298);
          top: rem(54);
          left: rem(28);
        }
        @media (min-width: 1600px) {
          width: rem(288);
          height: rem(288);
          left: rem(45);
        }
        @media (max-width: 992px) {
          left: 25%;
        }
        @media (max-width: 768px) {
          left: 27%;
          //width: rem(120);
          //height: rem(120);
          @include adaptiv-value("width", 520, 120, 1);
          @include adaptiv-value("height", 520, 120, 1);
        }
      }
    }
    &-elem {
      &-1 {
        position: absolute;
        bottom: rem(24.8);
        left: rem(-22.38);
        font-size: rem(12.1336);
        line-height: 2;
        letter-spacing: rem(.151669);
        transform: rotate(-90deg);
        @media (max-width: 992px) {
          display: none;
        }
        @media (min-width: 1600px) {
          bottom: rem(45.8);
          left: rem(26);
        }
      }

      &-2 {
        position: absolute;
        top: rem(41.42);
        right: rem(32.84);
        font-size: rem(9.41396);
        line-height: 2;
        letter-spacing: rem(.151669);
        @media (min-width: 992px) {
          top: rem(85);
          right: rem(36.8);
          font-size: rem(12.1336);
        }
        @media (min-width: 1600px) {
          right: rem(33.8);
        }
      }
    }
  }
  &__bg-elem {
    position: absolute;
    top: calc(50% - rem(30.33));
    right: rem(7);
    width: fit-content;
    font-size: rem(17.305);
    line-height: 2;
    letter-spacing: rem(.216312);
    color: #254746;
    transform: rotate(-90deg);
    @media (max-width: 992px) {
      display: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: rem(-34);
      right: rem(53.72);
      z-index: -1;
      width: rem(97.03);
      height: rem(97.03);
      background-color: var(--white);
      border-radius: 50%;
    }
  }
}