.support-block {
  padding-top: rem(67);
  @media (min-width: 1600px) {
    padding-top: rem(119);
  }
  .green-block.support-block__social {
    padding: rem(55) rem(14) rem(50) rem(28) !important;
    @media (min-width: 992px) {
      padding: rem(55) rem(14) rem(61) rem(38) !important;
    }
  }
  &__subtitle {
    width: 100%;
    max-width: rem(316);
    font-size: rem(20);
    @media (min-width: 992px) {
      font-size: rem(32);
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    @media (max-width: 992px) {
      flex-direction: column;
    }
    .support-item  {
      display: flex;
      align-items: center;
      justify-content: center;
      height: rem(30);
      min-height: rem(30);
      padding: 0 rem(19);
      background-color: transparent;
      text-decoration: none;
      @media (min-width: 992px) {
        height: rem(42);
        min-height: rem(42);
      }
      &:hover {
        color: var(--white);
      }
      img {
        max-height: rem(14);
        max-width: rem(14);
        @media (min-width: 992px) {
          max-height: rem(24);
          max-width: rem(24);
        }
      }
    }
  }
  &__img {
    padding-bottom: 0 !important;
    @media (min-width: 992px) {
      margin-left: rem(-100);
    }
    &-pc {
      max-height: rem(285);
      max-width: rem(274);
      //min-height: rem(602);
      //min-width: rem(424);
      @include contain;
      @media (min-width: 992px) {
        max-height: rem(602);
        max-width: rem(424);
        margin-bottom: rem(-60);
      }
    }
    &-mob {

    }
  }
  &__content {
    position: relative;
    width: 100%;
    max-width: rem(512);
    padding: rem(41) rem(22) rem(23) !important;
    @media (max-width: 992px) {
      margin: 0 auto;
    }
    @media (min-width: 992px) {
      padding: rem(64) 0 rem(77) rem(73) !important;
    }
  }
  &__main {
    width: 100%;
    @media (min-width: 992px) {
      max-width: 60%;
    }
    &-inner {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      overflow: hidden;
      @media (max-width: 992px) {
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }
  &__aside {
    width: 100%;
    @media (min-width: 992px) {
      max-width: 40%;
    }
  }
  &__bg-item {
    display: flex !important;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    max-height: 50%;
    @media (max-width: 992px) {
      display: none !important;
    }
  }
  &-description {
    padding-top: rem(5);
    @media (min-width: 992px) {
      padding-top: rem(30);
    }
    @media (max-width: 992px) {
      margin-bottom: rem(30) !important;
    }
    &-2 {
      padding-bottom: rem(10);
      @media (min-width: 992px) {
        padding-bottom: rem(30);
      }
    }
  }
  &-title {
    @media (max-width: 992px) {
      margin-bottom: 0 !important;
      line-height: 1.1;
    }
  }
}
.support-block {
  &--social {
    padding-top: rem(20) !important;
    @media (max-width: 992px) {
      padding-top: rem(30) !important;
    }
    .orange-block>div {
      padding-bottom: 0 !important;
    }
    .orange-block {
      overflow: hidden;
      @media (max-width: 992px) {
        padding: rem(58) rem(15) 0 !important;
      }
    }

    .support-block {
      &-title {
        @media (max-width: 992px) {
          width: auto;
          padding-bottom: rem(15);
        }
      }
      &-description {
        @media (max-width: 992px) {
          width: auto;
          margin-bottom: rem(15) !important;
          padding: 0 rem(15);
        }
      }
      &__img-pc {
        //@include contain;
        @media (min-width: 992px) {
          position: absolute;
          bottom: 0;
          right: rem(-60);
          min-width: rem(249.58);
          //max-width: rem(390);
          //min-width: rem(390);
          //margin-bottom: rem(-18);
        }
        @media (min-width: 1600px) {
          right: rem(-47);
          //max-width: rem(409);
          //max-height: rem(458);
          //min-width: rem(409);
          //min-height: rem(458);
          margin-bottom: rem(-36);
        }
        &-wrap {
          display: flex;
          align-items: flex-end;
        }
      }
      &__content {
        @media (min-width: 992px) {
          padding: 0 !important;
        }
      }
    }

    .support-items {
      @media (max-width: 992px) {
        padding: 0 !important;
      }
    }
    .support-item {
      @media (max-width: 992px) {
        margin-right: 0 !important;
      }
    }
    .choose-way-contact-label {
      @media (max-width: 992px) {
        margin-left: 0 !important;
        margin-right: 0 !important;
      }
    }
  }
  &__desc {
    @media (max-width: 992px) {
      margin-bottom: rem(40) !important;
    }
  }
  &__social.green-block {
    padding: 0 !important;
    @media (max-width: 992px) {
      min-height: auto !important;
      height: auto !important;
      padding: rem(41) rem(53) rem(53.99) !important;
    }
  }
}

.main-new-page {
  .weady-get-start {
    padding-bottom: rem(25) !important;
  }
}
