.weady-power {
  padding-top: rem(54);
  @media (min-width: 1600px) {
    padding-top: rem(120);
  }
  &__headline {
    width: 100%;
    max-width: rem(1000);
    padding-bottom: rem(23);
    line-height: 1.1;
  }
  &__desc {
    width: 100%;
    max-width: rem(478);
    font-size: rem(20);
    line-height: 1.32;
    letter-spacing: rem(.15);
    color: var(--white);
  }
  &__btn {
    padding: 0 rem(54.5);
    @media (max-width: 992px) {
      //height: rem(60);
      font-size: rem(16);
    }
    @media (max-width: 1600px) {
      font-size: rem(18);
    }
  }
  hr {
    margin: 0;
  }
  .dashboard-image {
    filter: drop-shadow(0 rem(91) rem(181) rgba(70, 92, 91, .15));
    border-radius: rem(10);
    overflow: hidden;
  }
}

.weady-need-to-power-block-description {
  width: 100%;
  max-width: rem(430);
  font-size: rem(18);
  @media (min-width: 1600px) {
    max-width: rem(478);
    font-size: rem(20);
  }
  @media (max-width: 992px) {
    padding-bottom: rem(20);
    font-size: rem(16);
  }
}