.about-hero {
  &-wrap {
    padding: rem(97) 0 rem(57);
    background-color: var(--orange);
    @media (min-width: 992px) {
      padding: rem(66) 0;
    }
    @media (min-width: 1600px) {
      padding: rem(121) 0 rem(126);
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    //max-width: rem(1030);
    max-width: rem(1305);
    margin: 0 auto;
    text-align: center;
    //@media (min-width: 1600px) {
    //  max-width: rem(1305);
    //}
  }
  &__subtitle {
    padding-bottom: rem(12);
    font-size: rem(16);
    line-height: 2;
    font-weight: 400;
    letter-spacing: rem(.15);
    text-transform: uppercase;
    color: var(--white);
    @media (min-width: 992px) {
      padding-bottom: rem(9);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(33);
      font-size: rem(22);
    }
  }
  &__headline {
    color: var(--white);
  }
  &__desc {
    width: 100%;
    //max-width: rem(433);
    max-width: rem(679);
    padding-top: rem(30);
    font-size: rem(16);
    line-height: 1.47;
    letter-spacing: rem(.15);
    color: var(--white);
    @media (min-width: 992px) {
      font-size: rem(18);
      padding-top: rem(20);
    }
    @media (min-width: 1600px) {
      //max-width: rem(679);
      padding-top: rem(54);
      font-size: rem(22);
    }
  }
}