.weady-see-options {
  &__btns {
    @media (max-width: 992px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  }
  .btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: rem(55);
    min-width: rem(318);
    max-width: rem(318);
    padding-top: 0;
    padding-bottom: 0;
    font-size: rem(16);
    //border: rem(1) solid var(--orange);
    @media (min-width: 992px) {
      height: rem(50);
      min-width: rem(318);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      height: rem(60);
      min-width: rem(379);
      //max-width: rem(379);
      font-size: rem(22);
    }
    @media (max-width: 992px) {
      margin-bottom: 0 !important;
    }
    &:hover {
      background: var(--white);
      //border-color: var(--green);
      color: var(--green) !important;
    }
    &+.btn {
      @media (max-width: 992px) {
        margin-top: rem(5);
      }
    }
  }
  &__img {
    max-width: rem(524);
    max-height: rem(333);
    @include contain;
    @media (min-width: 992px) {
      margin-right: rem(38);
    }
    @media (min-width: 1600px) {
      height: fit-content !important;
      max-width: rem(463);
      max-height: rem(406);
    }
    &-wrap {
      display: flex;
      justify-content: center;
      @media (min-width: 1600px) {
        align-items: flex-end;
        width: fit-content !important;
        min-width: rem(463);
        max-width: rem(463);
      }
    }
  }
  &__content {
    @media (max-width: 1400px) {
      align-items: flex-end;
    }
  }
}
.weady-see-options-title {
  @media (max-width: 1600px) {
    margin-bottom: rem(11) !important;
  }
  @media (max-width: 992px) {
    padding-bottom: rem(25);
    max-width: rem(355);
    margin-left: auto;
    margin-right: auto;
  }
}