.page-tariff-econom {
  .weady-create {
    @media (min-width: 992px) {
      padding-top: 6rem;
    }
  }
}

.weady-cards {
  .card {
    img {
      margin-right: rem(16) !important;
      margin-left: rem(16) !important;
    }
  }
}

.page-card {
  .card {
    height: auto !important;
    &-img {
      &-left {
        img {
          margin-left: auto !important;
          margin-right: rem(-1) !important;
          margin-top: rem(-3) !important;
        }
      }
      &-center {
        img {
          margin-left: auto !important;
          margin-right: auto !important;
        }
      }
    }
    .card-body {
      padding-bottom: rem(37);
    }
  }
}

.page-tariff {
  .weady-cards {
    margin-top: rem(55);
    @media (min-width: 992px) {
      margin-top: rem(97);
    }
    @media (min-width: 1600px) {
      margin-top: rem(115);
    }
  }

  .dashboard-card {
    @media (max-width: 992px) {
      padding-top: rem(37);
      padding-bottom: rem(50);
    }
    .card-body {
      padding-top: rem(47);
    }
  }

  .user-role-card,
  .language-card,
  .dashboard-card,
  .user-card,
  .analitics-card,
  .from-card,
  .blog-card {
    @media (min-width: 992px) {
      min-height: calc(44.5% - rem(24));
      max-height: calc(44.5% - rem(24));
    }
  }
  .font-card,
  .testimonial-card,
  .drag-n-drop-card,
  .utm-card,
  .crm-card {
    @media (min-width: 992px) {
      min-height: calc(55% - rem(24));
      max-height: calc(55% - rem(24));
    }
  }

  .color-card,
  .sitemap-card,
  .css-js-card,
  .gallery-card {
    @media (min-width: 992px) {
      min-height: calc(40% - rem(24));
      max-height: calc(40% - rem(24));
    }
  }

  .domain-card,
  .seo-card {
    @media (min-width: 992px) {
      min-height: calc(20% - rem(24));
      max-height: calc(20% - rem(24));
    }
  }

  .maintenance-card,
  .fg-login-card {
    @media (min-width: 992px) {
      min-height: calc(37% - rem(24));
      max-height: calc(37% - rem(24));
    }
  }
  .menu-card {
    @media (min-width: 992px) {
      min-height: calc(26% - rem(24));
      max-height: calc(26% - rem(24));
    }
  }
}

