.review {
  &-wrap {
    position: relative;
    z-index: 1;
    padding-top: rem(65);
    overflow: hidden;
    @media (min-width: 1600px) {
      padding-top: rem(103);
    }
  }
  &__headline {
    padding-bottom: rem(35);
    margin-bottom: 0;
    line-height: 1.1;
    color: var(--dark);
    @media (min-width: 992px) {
      padding-bottom: rem(49);
    }
    &:after {
      width: rem(113);
      @media (min-width: 992px) {
        width: 62%;
      }
      @media (min-width: 1200px) {
        width: 29%;
      }
      @media (min-width: 1290px) {
        width: 62%;
      }
      @media (min-width: 1400px) {
          @include adaptiv-value("width", 940, -5200, 1);
      }
      @media (min-width: 1600px) {
        width: 62%;
      }
    }
    b {
      color: var(--orange);
    }
  }
  .mCSB_container,
  &__content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    max-height: rem(1144);
    //min-height: rem(1144);
    //@include scrollbar-x-block;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: rem(383);
    padding-left: rem(40.7);
    padding-right: rem(40.7);
    padding-bottom: rem(79);
    margin: rem(7.5) rem(6.5);
    background-color: var(--grey);
    color: var(--dark);
    @media (min-width: 992px) {
      max-width: rem(395);
      padding-left: rem(42);
      padding-right: rem(42);
    }
    @media (min-width: 1600px) {
      max-width: rem(526);
      padding-left: rem(56);
      padding-right: rem(56);
      margin: rem(9.5) rem(8);
    }
    &:nth-child(4n + 1) {
      min-height: rem(369);
      max-height: rem(369);
      padding-top: rem(30);
      @media (min-width: 1600px) {
        min-height: rem(491);
        max-height: rem(491);
        padding-top: rem(41);
      }
    }
    &:nth-child(4n + 2) {
      min-height: rem(453);
      max-height: rem(453);
      padding-top: rem(85);
      @media (min-width: 1600px) {
        padding-top: rem(114);
        min-height: rem(603);
        max-height: rem(603);
      }
    }
    &:nth-child(4n + 3) {
      min-height: rem(453);
      max-height: rem(453);
      padding-top: rem(85);
      @media (min-width: 1600px) {
        padding-top: rem(114);
        min-height: rem(603);
        max-height: rem(603);
      }
    }
    &:nth-child(4n + 4) {
      min-height: rem(369);
      max-height: rem(369);
      padding-top: rem(30);
      @media (min-width: 1600px) {
        min-height: rem(491);
        max-height: rem(491);
        padding-top: rem(41);
      }
    }
  }
  &__head {

  }
  &__img {
    width: rem(48);
    height: rem(48);
    border-radius: 50%;
    overflow: hidden;
    margin-right: rem(14);
    @media (min-width: 992px) {
      margin-right: rem(15);
    }
    @media (min-width: 1600px) {
      width: rem(64);
      height: rem(64);
      margin-right: rem(20);
    }
  }
  &__head {
    display: flex;
    padding-bottom: rem(27);
    @media (min-width: 1600px) {
      padding-bottom: rem(36);
    }
    &-desc {

    }
  }
  &__title {
    font-weight: 700;
    font-size: rem(15);
    line-height: 1;
    letter-spacing: .110124px;
    @media (min-width: 1600px) {
      font-size: rem(21);
    }
  }
  &__subtitle {
    padding-top: rem(3.5);
    font-weight: 700;
    font-size: rem(12);
    line-height: 1.35;
    letter-spacing: .110124px;
    @media (min-width: 1600px) {
      padding-top: rem(5);
      font-size: rem(16);
    }
  }
  &__text {
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.110124);
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
  &__logo {
    max-width: rem(185);
    max-height: rem(45);
    @include contain;
    margin-top: rem(25);
  }
}

.program  {
  .slide-nav-img {
    display: none;
    max-width: rem(195);
    max-height: rem(33);
    min-width: rem(195);
    min-height: rem(33);
    @include contain;
    @media (min-width: 992px) {
      max-width: rem(284);
      max-height: rem(48);
      min-width: rem(284);
      min-height: rem(48);
    }
  }
}

