.search-items {
  &-wrap {
    padding: rem(85) 0 rem(67.25);
    @media (min-width: 768px) {
      padding: rem(68) 0 rem(64);
    }
    @media (min-width: 1600px) {
      padding: rem(84) 0 rem(51);
    }
  }
  &__headline {
    text-align: center;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    padding-top: rem(24);
    margin: 0 rem(-12);
    @media (min-width: 768px) {
      padding-top: rem(29);
    }
    @media (min-width: 1600px) {
      padding-top: rem(39);
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: rem(452);
    margin: 0 auto rem(24.25);
    padding: rem(38.9) rem(30.37) rem(43.35);
    border: rem(1) solid #5B8473;
    border-radius: rem(7);
    @media (min-width: 768px) {
      padding: rem(44) rem(52) rem(54);
      margin: 0 auto rem(23);
    }
    @media (min-width: 992px) {
      max-width: calc(100% / 2 - rem(24));
      margin: 0 rem(12) rem(23);
    }
    @media (min-width: 1220px) {
      max-width: calc(100% / 3 - rem(24));
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(35);
    }
  }
  &__title {
    font-weight: 700;
    font-size: rem(26);
    line-height: 1;
    letter-spacing: rem(.15);
    color: #313C36;
    @media (min-width: 768px) {
      font-size: rem(24);
    }
    @media (min-width: 1600px) {
      font-size: rem(32);
    }
    &-wrap {
      display: flex;
      align-items: center;
      padding-bottom: rem(16.48);
      @media (min-width: 768px) {
        padding-bottom: rem(32);
      }
      @media (min-width: 1600px) {
        padding-bottom: rem(45);
      }
    }
    &-ico {
      max-width: rem(29);
      max-height: rem(35);
      object-fit: contain;
      margin-right: rem(18);
      @media (min-width: 1600px) {
        max-width: rem(33);
        max-height: rem(40);
      }
    }
  }
  ul {
    padding-bottom: rem(25);
    @media (min-width: 768px) {
      padding-bottom: rem(39);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(13);
    }
  }
  li {
    font-size: rem(16);
    line-height: 1.98;
    letter-spacing: rem(.15);
    color: #7B7B7B;
    @media (min-width: 768px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
  &__subtitle {
    padding-bottom: rem(12.59);
    text-align: center;
    font-size: rem(16);
    line-height: 1.98;
    letter-spacing: rem(.15);
    color: #466C61;
    @media (min-width: 768px) {
      font-size: rem(20);
      padding-bottom: rem(9.27);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(20);
      font-size: rem(22);
    }
  }
  &__btn {
    display: flex;
    width: fit-content;
    height: rem(50);
    min-width: rem(224);
    margin: 0 auto;
    @media (min-width: 1600px) {
      min-width: rem(267);
      height: rem(60);
    }
  }
  .list-img li:before {
    background-image: url("../img/ico/Check.webp");
  }
}