.weady-website-manager {
  &__img {
    &-wrap {
      position: relative;
    }
    &-dop {
      position: absolute;
      bottom: rem(-10);
      right: 0;
      @include adaptiv-value("max-height", 462, 150, 1);
      @include adaptiv-value("max-width", 503, 150, 1);
      //min-height: rem(462);
      //min-width: rem(503);
      @include contain;
      @media (max-width: 768px) {
        bottom: rem(42);
      }
      @media (min-width: 992px) {
        bottom: rem(71);
        @include adaptiv-value("right", 123, -300, 1);
      }
    }
  }
}
.weady-manage-title {
  @media (min-width: 992px) {
    padding-right: rem(15);
  }
}
.weady-manage-title,
.weady-get-start-title,
.weady-websites-title {
  @media (max-width: 768px) {
    width: 100%;
    max-width: none !important;
    font-size: 2.2rem;
  }
}
.weady-simple-package {
  @media (max-width: 992px) {
    padding-top: rem(30);
  }
}
.steps__desc,
.weady-simple-package-description,
.weady-manage-description {
  @media (max-width: 1200px) {
    font-size: rem(16);
  }
}
.weady-websites-header,
.weady-get-start-header,
.weady-simple-package-header,
.weady-manage-website-header {
  @media (min-width: 992px) {
    flex-direction: row !important;
  }
}

.weady-website-manager-items {
  @media (max-width: 992px) and (max-width: 1200px) {
    max-width: rem(350);
  }
}


.weady-manage-title {
  width: rem(460);
  @media (min-width: 992px) {
    line-height: 1;
  }
  @media (min-width: 1200px) {
    width: rem(700);
  }
  @media (min-width: 1600px) {
    width: rem(791);
  }
}