.partner-page {
  .header-image-1 {
    background-image: url("../img/bg/bg.svg");
    background-size: cover;
    top: 0;
    height: rem(870);
    @media (min-width: 395px) {
      height: rem(740);
    };
    @media (min-width: 485px) {
      height: rem(650);
    }
    @media (min-width: 477px) {
      height: rem(600);
    }
    @media (min-width: 992px) {
      height: rem(782);
    }
    @media (min-width: 1400px) {
      background-size: 100vw 100%;
    }
    @media (min-width: 1600px) {
      height: rem(885);
    }
    @media (min-width: 1800px) {
      height: rem(932);
    }
    @media (min-width: 1920px) {
      height: rem(1000);
    }
  }
}

.hero-partner {
  position: relative;
  height: 100%;
  min-height: 100%;
  &-wrap {
    margin-top: rem(-67);
    //background-image: url("../img/bg/bg.svg");
    //background-repeat: no-repeat;
    //background-size: cover;
    //background-position: center;
    @media (min-width: 1200px) {
      margin-top: rem(-85);
      //background-size: auto 100%;
    }
    //@media (min-width: 1600px) {
    //  //background-size: auto 100%;
    //}
    @media (min-width: 1200px) {
      background-size: 100% 100%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: rem(818);
    margin: 0 auto;
    padding-top: rem(113);
    padding-bottom: rem(140);
    text-align: center;
    @media (min-width: 992px) {
      padding-top: rem(160);
      padding-bottom: rem(153);
    }
    @media (min-width: 1600px) {
      max-width: rem(1007);
      padding-top: rem(144);
      padding-bottom: rem(276);
    }
  }
  &__headline {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
    font-weight: 900;
    font-size: rem(42);
    line-height: 1.1;
    color: var(--white);
    @media (min-width: 992px) {
      font-size: rem(64);
    }
    @media (min-width: 1600px) {
      font-size: rem(76);
    }
    &-img {
      display: inline-block;
      width: 100%;
      height: 100%;
      max-width: rem(190);
      max-height: rem(75);
      margin: rem(-47) rem(5) 0;
      vertical-align: middle;
      @include contain;
      @media (min-width: 992px) {
        max-height: rem(94);
        max-width: rem(200);
        margin: rem(-50) rem(15) 0;
      }
      @media (min-width: 1600px) {
        max-width: rem(285);
        max-height: rem(127);
        margin-top: rem(-80);
      }
    }
  }
  &__desc {
    width: 100%;
    padding: rem(20) 0 rem(45);
    font-size: rem(16);
    line-height: 1.47;
    letter-spacing: .15px;
    color: var(--white);
    @media (min-width: 992px) {
      padding: rem(30) 0;
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      max-width: rem(931);
      font-size: rem(22);
      line-height: 1.64;
    }
  }
  &__btn {
    height: rem(55);
    padding: 0 rem(40);
    font-size: rem(16);
    @media (min-width: 992px) {
      height: rem(50);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      height: rem(55);
      font-size: rem(22);
    }
  }
  &__bg-item {
    position: absolute;
    width: fit-content;
    height: fit-content;
    @media (max-width: 992px) {
      display: none;
    }
    &--1 {
      left: rem(0);
      bottom: rem(171);
      transform: rotate(-90deg);
      @media (min-width: 992px) {
        left: rem(-50);
      }
      @media (min-width: 1200px) {
        left: rem(00);
      }
      @media (min-width: 1600px) {
        left: rem(-60);
        bottom: rem(320);
      }
      .hero-partner__bg-item {
        &-img {
          max-width: rem(128);
          max-height: rem(128);
          min-width: rem(128);
          min-height: rem(128);
          @media (min-width: 1600px) {
            max-width: rem(214);
            max-height: rem(214);
            min-width: rem(214);
            min-height: rem(214);
          }
        }
        &-desc {
          top: 0;
          margin-top: rem(47);
          margin-left: rem(83);
          font-size: rem(17.25);
          @media (min-width: 1600px) {
            margin-top: rem(90);
            margin-left: rem(169);
          }
        }
      }
    }
    &--2 {
      top: rem(200);
      right: rem(20);
      transform: rotate(90deg);
      @media (min-width: 992px) {
        right: rem(-10);
      }
      @media (min-width: 1200px) {
        right: rem(20);
      }
      @media (min-width: 1600px) {
        top: rem(243);
      }
      .hero-partner__bg-item {
        &-img {
          max-width: rem(53);
          max-height: rem(53);
          min-width: rem(53);
          min-height: rem(53);
          @media (min-width: 1600px) {
            max-width: rem(69);
            max-height: rem(69);
            min-width: rem(69);
            min-height: rem(69);
          }
        }
        &-desc {
          top: 0;
          left: 0;
          margin-top: rem(15);
          margin-left: rem(29);
          font-size: rem(9);
          @media (min-width: 1600px) {
            right: 0;
            margin-top: 0;
            margin-right: rem(11);
            font-size: rem(17.25);
          }
        }
      }
    }
    &-img {
      @include contain;
    }
    &-desc {
      position: absolute;
      line-height: 2;
      letter-spacing: .215625px;
      color: rgba(236, 236, 236, .87);
      white-space: nowrap;
    }
  }
}