@use "sass:math";

@mixin cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@mixin scrollbar-block {
  overflow: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }
}

@mixin scrollbar-x-block {
  @include scrollbar-block;
  overflow-x: scroll;
  overflow-y: hidden;
}

@mixin scrollbar-y-block {
  @include scrollbar-block;
  overflow-x: hidden;
  overflow-y: scroll;
}

@function opacity($color, $opacity) {
  $result: rgba($color, $opacity);
  @return $result;
}

//TO %
@function prc($pxOne, $pxTwo) {
  $result: math.div($pxOne, $pxTwo) * 100%;
  @return $result;
}
//TO REM
@function rem($px) {
  $result: math.div($px, 16) + rem;
  @return $result;
}
//TO EM
@function em($px) {
  $result: math.div($px, 16) + em;  //Возвращает результат деления $px на 16.
  @return $result;
}

// Адаптивное свойство
$maxWidth: 1920;
$maxWidthContainer: 1170;

@mixin adaptiv-value($property, $startSize, $minSize, $type) {
  $addSize: $startSize - $minSize;
  @if $type==1 {
    //Только если меньше контейнера
    #{$property}: $startSize + px;
    @media (max-width: #{$maxWidthContainer + px}) {
      #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidthContainer - 320}));
    }
  }
  @if $type==2 {
    //Только если больше контейнера
    #{$property}: $startSize + px;
    @media (max-width: #{$maxWidthContainer + px}) {
      #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
    }
  }
  @if $type==3 {
    //Всегда
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
  }
}

