.review {
  &-hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-wrap {
      padding: rem(177) 0 rem(159);
      background: linear-gradient(233.22deg, rgba(80, 118, 100, 0.7) -39.68%, rgba(120, 173, 148, 0.7) -23.23%, rgba(59, 99, 80, 0.7) -8.1%, rgba(70, 112, 92, 0.7) 7.69%, rgba(47, 85, 67, 0.7) 22.83%, rgba(89, 126, 109, 0.7) 37.96%, rgba(128, 174, 153, 0.7) 53.75%, rgba(80, 114, 98, 0.7) 70.2%, rgba(67, 95, 82, 0.7) 86.65%), #204242;
      text-align: center;
      color: var(--white);
      @media (min-width: 992px) {
        padding: rem(77) 0 rem(124);
      }
      @media (min-width: 1600px) {
        padding: rem(99) 0 rem(133);
      }
    }
    &__headline {
      width: 100%;
      max-width: rem(850);
      color: var(--white);
      @media (min-width: 1600px) {
        max-width: rem(1117);
      }
    }
    &__desc {
      width: 100%;
      max-width: rem(640);
      padding-bottom: rem(19);
      font-size: rem(16);
      line-height: 1.64;
      letter-spacing: rem(.15);
      @media (min-width: 992px) {
        padding: rem(31) 0 rem(14);
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        padding: rem(11) 0 rem(46);
        font-size: rem(22);
      }
    }
    &__img {
      width: 100%;
      height: 100%;
      max-width: rem(198.67);
      max-height: rem(45);
      @include cover;
      @media (min-width: 992px) {
        max-width: rem(238);
        max-height: rem(54);
      }
      @media (min-width: 1600px) {
        max-width: rem(309.04);
        max-height: rem(70);
      }
    }
  }
}