.why-weady-description {
  margin-bottom: 0;
  padding-bottom: 0;
  font-size: rem(18) !important;
  @media (min-width: 992px) {
    padding-bottom:  rem(34);
  }
  @media (min-width: 1600px) {
    padding-bottom: rem(52.55);
    font-size: rem(24);
  }
}
.weady-websites-author-review {
  padding-bottom: rem(45);
}

.weady-start-create-site-block-header-one-page {
  @media (min-width: 992px) {
    width: 760px;
  }
  @media (min-width: 1200px) {
    width: 1070px;
  }
}
.why-weady {
  &__desc {
    @media (min-width: 992px) {
      width: fit-content !important;
      max-width: 50%;
    }
    .orange-section {
      max-width: rem(590);
      @media (min-width: 992px) {
        padding: 3rem!important;
      }
      @media (min-width: 1600px) {
        max-width: rem(590);
      }
    }
  }
  &__left {
    @media (min-width: 992px) {
      width: 50%;
    }
    img {
      @media (min-width: 992px) {
        height: calc(100% - rem(25)) !important;
        object-fit: cover;
      }
    }
  }
  &__user {
    &-img {
      max-width: rem(41);
      max-height: rem(41);
      min-width: rem(41);
      min-height: rem(41);
      @include cover;
      margin-right: rem(22);
      border-radius: 50%;
      overflow: hidden;
      @media (min-width: 992px) {
        max-width: rem(72);
        max-height: rem(72);
        min-width: rem(72);
        min-height: rem(72);
      }
    }
  }
  &__img {
    &-mark {
      max-width: rem(109);
      max-height: rem(27);
      min-width: rem(109);
      min-height: rem(27);
      @include contain;
      @media (min-width: 992px) {
        max-width: rem(185);
        max-height: rem(45);
        min-width: rem(185);
        min-height: rem(45);
      }
    }
  }
  .orange-section {
    @media (max-width: 992px) {
      padding: rem(28) rem(37.75) rem(82) rem(30);
    }
  }
}

.weady-websites-author-name {
  @media (max-width: 992px) {
    margin-bottom: rem(4);
  }
}
.weady-website-author {
  position: relative;
  &__ico {
    position: absolute;
    top: calc(rem(-30) + rem(-48));
    right: calc(rem(25.85) + rem(-48));
    max-width: rem(66.27);
    max-height: rem(61);
    @media (min-width: 992px) {
      top: calc(rem(-45.53) + rem(-47));
      right: calc(rem(35) + rem(-48));
      max-width: rem(100);
      max-height: rem(92);
    }
  }
}