.content {
  position: relative;
  &-wrap {
  }
  &__headline {
    line-height: 1.1;
    @media (min-width: 992px) {
      max-width: 95%;
    }
    @media (min-width: 1200px) {
      max-width: rem(964);
    }
    &:after {
      width: rem(82);
      @media (min-width: 992px) {
        width: rem(151);
      }
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-top: rem(18);
    @media (min-width: 992px) {
      padding-top: rem(85);
      flex-direction: row;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 rem(12);
    border: rem(1) solid #D5D5D5;
    box-shadow: 0 rem(44) rem(91) rgba(0, 0, 0, .05);
    border-radius: rem(15);
    overflow: hidden;
    @media (min-width: 992px) {
      max-width: calc(50% - rem(23));
      padding: 0 rem(5);
      justify-content: space-between;
    }
    &:not(:last-child) {
      @media (max-width: 992px) {
        margin-bottom: rem(35);
      }
    }
    &-content {
      padding: rem(22) rem(17) rem(17) rem(13);
      @media (min-width: 992px) {
        padding: rem(48) rem(43) rem(35);
      }
    }
  }
  &__btn {
    padding: 0 rem(20);
    height: rem(45);
    @media (min-width: 992px) {
      height: rem(64);
      padding: 0 rem(40);
    }
  }
  &__title {
    padding-top: rem(29);
    line-height: 1.1;
    @media (min-width: 992px) {
      padding-top: rem(32);
    }
  }
  &__desc {
    padding-top: rem(35);
    padding-left: 0;
    &-item {
      display: flex;
      &:not(:last-child) {
        padding-bottom: rem(14);
      }
    }
  }
  &__ico {
    max-height: rem(30);
    max-width: rem(30);
    //min-height: rem(30);
    //min-width: rem(30);
    margin-right: rem(7);
    margin-top: rem(7);
    @include contain;
    width: auto;
    height: auto;
  }
  &__text {
    font-size: rem(18);
    line-height: 1.64;
    letter-spacing: rem(.15);
    color: var(--grey2);
    @media (min-width: em(992)) {
      font-size: rem(22);
    }
  }
  &__img {
    max-width: 100%;
    max-height: rem(101);
    //margin: 0 rem(15);
    @include cover;
    @media (min-width: em(992)) {
      max-height: rem(176);
      min-height: rem(176);
    }
  }
  &__bg {
    &-item {
      position: absolute;
      right: rem(18);
      top: rem(65);
      transform: rotate(90deg);
      display: flex;
      @media (max-width: em(992)) {
        display: none;
      }
    }
    &-ico {
      max-width: rem(76);
      max-height: rem(76);
      min-width: rem(76);
      min-height: rem(76);
      @include contain;
    }
    &-text {
      position: absolute;
      right: rem(22);
      top: rem(15);
      white-space: nowrap;

      font-size: rem(17.25);
      line-height: 2;
      letter-spacing: rem(.215625);
      color: var(--green);
    }
  }
}