.steps{
  position: relative;
  display: flex;
  justify-content: center;
  //@media (min-width: 768px) {
  //  height: calc( 100vh + 860px );
  //}
  @media (min-width: 992px) {
    height: calc( 100vh + 610px );
  }
  @media (min-width: 1200px) {
    height: calc( 100vh + 800px );
  }
  @media (min-width: 1440px) {
    height: calc( 100vh + 760px );
  }
  &-wrap {
    position: relative;
  }
  &__headline {
    @media (min-width: 992px) {
      position: absolute;
      top: rem(20);
    }
    @media (min-width: 1200px) {
      top: rem(-50);
    }
  }
  &-md {
    //height: rem(600);
    width: 100%;
    max-width: 100%;
    padding-bottom: rem(88);
    @media (min-width: 992px) {
      display: none;
    }
    &__content {

    }
    &__item {
      @media (max-width: 992px) {
        display: flex;
        flex-direction: column;
      }
    }
    &__desc {
      display: flex;
    }
    &__img {

    }
  }

  .white-button:hover {
    color: var(--white) !important;
  }
}

.how-it-work-block__img {
  width: 100%;
  max-width: 100%;
}

.steps__content-wrap {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  //padding-top: 52vh;
  //padding-bottom: rem(300);
  @media (min-height: 600px) and (max-width: 768px) {
    padding-top: rem(0);
  }
  @media (max-width: 992px) {
    display: flex;
    flex-direction: column;
  }
  @media (min-width: 992px) {
    position: sticky;
    top: 0;
    height: 100vh;
    //padding-top: rem(256);
    //padding-bottom: rem(230);
    padding-bottom: 0;
    padding-top: rem(180);
  }
  @media (min-width: 1200px) {
    //padding-top: rem(550);
    padding-top: rem(420);
    padding-bottom: rem(350);
  }
  @media (min-width: 1440px) {
    padding-top: rem(550);
  }
}

.steps .steps__content{
  max-height: 100vh;
  width: 100vw;
  display: flex;
  @media (max-width: 992px) {
    display: none;
  }
}
.steps .steps__content .steps__text [class^=stepItem].element-show {
  //background-color: red;
  transition: all 1.5s;
}
.steps .steps__content .steps__imgs {
  position: relative;
}

.how-it-work-block__img {
  @media (max-width: 1200px) {
    background-color: #FD804C;
  }
}

//.steps .steps__content .steps__text [class^=stepItem]{
//  max-height: 18vh !important;
//}
.steps .steps__content .steps__imgs [class^=stepItem]{
  position: absolute;
  //top: 0%;
  right: 0;
  opacity: 0;
  transform: translateY(10%);
  //transform: translateX(16%);
  //@media (min-width: 1200px) {
  //  right: -16%;
  //}
}
.steps .steps__content .steps__imgs [class^=stepItem].element-show {
  opacity: 1;
  transition: transform 1.5s;
  transform: translateY(0%);
  //transform: translateX(16%);
  //@media (min-width: 1200px) {
  //  right: -16%;
  //}
}