.table-price {
  display: flex;
  flex-direction: column;
  @media (min-width: 992px) {
    align-items: center;
  }
  &-wrap {
    padding: rem(50) 0;
    @media (max-width: 992px) {
      overflow: hidden;
    }
  }
  &__headline {
    width: 100%;
    max-width: rem(842);
    padding-bottom: rem(30);
    @media (min-width: 992px) {
      text-align: center;
    }
    @media (min-width: 1600px) {
      max-width: rem(1036);
      padding-bottom: rem(50);
    }
  }
  &__content {
    width: 100%;
    @media (max-width: 992px) {
      @include scrollbar-x-block;
      white-space: nowrap;
    }
  //  &--fixed {
  //    position: fixed;
  //    top: 0;
  //    z-index: 1;
  //    @media (max-width: 992px) {
  //      padding: 0 !important;
  //      //margin: 0 !important;
  //    }
  //    tbody {
  //      display: none;
  //    }
  //  }
  }
  table {
    width: 100%;
    border-radius: rem(6) rem(6) 0 0;
    overflow: hidden;
    @media (max-width: 992px) {
      min-width: rem(1001);
    }
    tr {
      height: rem(54);
    }
    th,
    td {
      padding: rem(0) rem(18);
    }
    thead {
      //position: sticky;
      //top: 0;
      width: 100%;
      min-width: 100%;
      border: rem(1) solid rgba(76, 78, 100, .12);
      background-color: #F5F5F7;
      font-weight: 700;
      font-size: rem(20);
      line-height: 1.2;
      letter-spacing: rem(.15);
      color: rgba(76, 78, 100, .87);
      tr {
      }
      td {
      }
    }
    tbody {
      tr {
        border: rem(1) solid rgba(76, 78, 100, .12);
        font-size: rem(18);
        line-height: 2.17;
        letter-spacing: rem(.15);
        color: #282828;
        &:nth-last-child(2) td {
          font-weight: 700;
          color: #000000;
        }
        &:last-child {
          color: #000000;
          background-color: #EFF4F2;
        }
        span {
          font-size: rem(14);
          line-height: 2.79;
          letter-spacing: rem(.15);
          color: #A5A5A5;
        }
      }
      td {
        &:first-child {font-weight: 700;
          font-size: rem(20);
          line-height: 1.95;
          color: #6E8E8E;
          &:after {
            content: '?';
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: rem(20);
            height: rem(20);
            max-width: rem(20);
            max-height: rem(20);
            min-width: rem(20);
            min-height: rem(20);
            margin: rem(-5) 0 0 rem(20);
            background-color: var(--green7);
            border-radius: 50%;
            color: var(--white);

            font-weight: 500;
            font-size: rem(12);
            line-height: 1.67;
            text-align: center;
            letter-spacing: rem(.14);
            cursor: pointer;
          }
        }
      }
    }
  }
}