.modal-ui {
  position: relative;
  margin: 15px;
  max-height: 95vh;
   @include scrollbar-x-block;
  //&__close {
  //  position: absolute;
  //  cursor: pointer;
  //  display: block;
  //  background-color: transparent;
  //}

  &-wrap {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    &--score {
      background-color: rgba(0,0,0,.7);
      .modal {
        width: 100%;
        max-width: 720px;
        padding: 44px 30px 44px 44px;
        background-color: #fff;
        border: 1px solid #EFF2F7;
        box-shadow: 0 4px 40px rgba(79, 79, 79, .2);
        border-radius: 4px;
      }
      .modal__close {
        top: 30px;
        right: 30px;
        max-width: 16px;
        max-height: 16px;
        width: 100%;
        height: 100%;
        // @include contain;
      }
    }
  }
}