.type {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 1200px) {
    max-width: rem(450);
  }
  &-wrap {
    padding: rem(58) 0;
    @media (min-width: 992px) {
      padding: rem(28) 0;
    }
    @media (min-width: 1600px) {
      padding: rem(46) 0;
    }
  }
  &__headline {
    text-align: center;
  }
  &__desc {
    width: 100%;
    max-width: rem(644);
    padding: rem(22) 0 rem(46);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    color: #38604D;
    text-align: center;
    @media (min-width: 992px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      max-width: rem(663);
      font-size: rem(22);
    }
  }
  &__content {
    justify-content: space-between;
    padding-top: rem(45);
    @media (max-width: 1200px) {
      flex-direction: column;
    }
  }
  &__nav {
    display: flex;
    align-items: center;
    width: 100%;
    @media (min-width: 1200px) {
      max-width: 68%;
    }
    @media (max-width: 1200px) {
      flex-direction: column;
    }
    &-wrap {
      display: flex;
      align-items: center;
      padding: rem(24) rem(59) rem(37);
      border: rem(1) solid #5D8674;
      //filter: drop-shadow(0 rem(44) rem(91) rgba(0, 0, 0, .22));
      border-radius: 15px;
      @media (max-width: 1200px) {
        flex-direction: column;
      }
      @media (min-width: 1200px) {
        padding: rem(20) rem(15) rem(20) rem(33);
      }
    }
    &-subtitle {
      width: 100%;
      font-weight: 900;
      font-size: rem(20);
      line-height: 1.17;
      letter-spacing: rem(.15);
      color: #313C36;
      @media (min-width: 1200px) {
        max-width: 32%;
        padding-right: rem(35);
        font-size: 1.5rem;
      }
      @media (min-width: 1600px) {
        font-size: rem(32);
      }
      @media (max-width: 1200px) {
        text-align: center;
        padding-bottom: rem(20);
      }
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      height: rem(50);
      width: 100%;
      padding: 0 rem(25);
      font-size: rem(16);
      line-height: 1.64;
      text-align: center;
      letter-spacing: rem(.15);
      @media (min-width: 992px) {
        height: rem(54);
        padding: 0 rem(15);
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
      &-wrap {
        width: 100%;
        max-width:  rem(216);
        margin: 0 rem(3.5);
        background-color: #D9D9D9;
        color: #313C36;
        border-radius: rem(10);
        @media (min-width: 1200px) {
          max-width: calc(100% / 3 - rem(2.3));
        }
        &:hover,
        &.active {
          background: linear-gradient(194.71deg, #FB6D33 9.85%, #FF9569 130%);
          color: var(--white);
        }
        & + .type__nav-item-wrap {
          @media (max-width: 1200px) {
            margin-top: rem(16);
          }
        }
      }
    }
  }
  &__item {
    width: 100%;
    padding: rem(35) rem(25) rem(46.5);
    border: rem(1) solid #5D8674;
    //filter: drop-shadow(0 rem(44) rem(91) rgba(0, 0, 0, .22));
    border-radius: rem(15);
    @media (min-width: 420px) {
      padding: rem(35) rem(59.4) rem(46.5);
    }
    @media (min-width: 992px) {
      padding: rem(35) rem(46) rem(59.5);
    }
    @media (min-width: 1200px) {
      max-width: calc(100% / 3 - rem(16.6));
    }
    @media (min-width: 1600px) {
      padding: rem(35) rem(25) rem(55);
    }
    & + .type__item {
      @media (max-width: 1200px) {
        margin-top: rem(18);
      }
    }
  }
  &__title {
    padding-bottom: rem(2);
    font-weight: 900;
    font-size: rem(32);
    line-height: 1.334;
    color: rgba(76, 78, 100, .87);
    text-align: center;
  }
  &__subtitle {
    padding-bottom: rem(3);
    font-size: rem(16);
    line-height: 1.2;
    letter-spacing: rem(.15);
    color: #7B7B7B;
    text-align: center;
    @media (min-width: 922px) {
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      font-size: rem(22);
    }
  }
  &__price {
    padding-top: rem(15);
    text-align: center;
    font-weight: 900;
    font-size: rem(17);
    line-height: 1.24;
    letter-spacing: rem(.15);
    color: rgba(76, 78, 100, .6);
    @media (min-width: 922px) {
      padding-bottom: rem(4);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(15);
    }
    b {
      font-weight: 900;
      font-size: rem(40);
      line-height: 1.4;
      color: var(--green7);
    }
    &-desc {
      padding-bottom: rem(16);
      font-size: rem(16);
      line-height: 1.2;
      letter-spacing: rem(.15);
      color: #FB6733;
      text-align: center;
      @media (min-width: 922px) {
        padding-bottom: rem(26);
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        padding-bottom: rem(43);
        font-size: rem(22);
      }
    }
  }
  ul {
    padding: 0 0 rem(43) 0;
  }
  li {
    color: #7B7B7B;
  }
  .btn-ui {
    & + .btn-ui {
      margin-top: rem(20);
      @media (min-width: 922px) {
        margin-top: rem(18);
      }
      @media (min-width: 1600px) {
        margin-top: rem(14);
      }
    }
  }
  &__btn {
    width: 100%;
  }
}

.list-img li {
  display: flex;
  line-height: 1.98;
  letter-spacing: rem(.15);
  //list-style-image: url("../img/ico/Check2.webp");
  padding-left: rem(3);
  font-size: rem(16);
  @media (min-width: 922px) {
    font-size: rem(18);
  }
  @media (min-width: 1600px) {
    font-size: rem(22);
  }
  &:before {
    content: '';
    width: rem(23);
    height: rem(23);
    min-width: rem(23);
    min-height: rem(23);
    max-width: rem(23);
    max-height: rem(23);
    margin-top: rem(7);
    margin-right: rem(9.61);
    background-size: contain;
    background-image: url("../img/ico/Check2.webp");
    background-repeat: no-repeat;
  }
}