.help-hero {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  @media (max-width: 992px) {
    flex-direction: column;
    text-align: center;
  }
  &-wrap {
    color: #FFFFFF;
    background: linear-gradient(0deg, #FD804C, #FD804C), #5B8473;
  }
  &__content {
    width: 100%;
    max-width: rem(655);
    padding: rem(100) 0 rem(51);
    @media (min-width: 992px) {
      padding: rem(77) 0 rem(89);
    }
    @media (min-width: 1600px) {
      padding: rem(77) 0 rem(140);
    }
  }
  &__headline {
    color: #FFFFFF;
    @media (max-width: 992px) {
      padding-bottom: rem(30);
      text-align: left;
    }
  }
  &__desc {
    display: flex;
    width: 100%;
    max-width: rem(490);
    padding: rem(5) 0 rem(30);
    font-size: rem(16);
    line-height: 1.64;
    letter-spacing: rem(.15);
    text-align: left;
    @media (min-width: 992px) {
      padding: rem(17) 0 rem(28);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {;
      font-size: rem(22);
    }
    &:before {
      content: '';
      //display: flex;
      height: rem(1);
      width: rem(140);
      min-width: rem(140);
      margin-right: rem(30);
      margin-top: rem(15);
      background-color: white;
      @media (max-width: 992px) {
        margin-left: rem(-45);
      }
    }
  }
  &__search {
    input {
      height: rem(60);
      width: 100%;
      max-width: rem(655);
      padding: 0 rem(19);
      background-color: #FFFFFF;
      border-radius: rem(10);
      &::placeholder {
        font-size: rem(16);
        line-height: 1.85;
        letter-spacing: rem(.229861);
        color: #5D8674;
        opacity: 0.6;
        @media (min-width: 992px) {
          font-size: rem(18);
        }
        @media (min-width: 1600px) {;
          font-size: rem(22);
        }
      }
    }
  }
  &__img {
    max-width: rem(501);
    max-height: rem(368);
    @include contain;
    @media (min-width: 992px) {
      margin-right: rem(80);
    }
    @media (min-width: 1600px) {
      max-width: rem(533);
      max-height: rem(428);
    }
  }
  &__elem-bg {
    position: absolute;
    top: rem(115);
    right: rem(10);
    font-weight: 400;
    font-size: rem(7.65);
    line-height: 5;
    letter-spacing: rem(.215625);
    color: #204242;
    transform: rotate(90deg);
    @media (min-width: 992px) {
      right: rem(30);
      font-size: rem(14);
    }
    @media (min-width: 1600px) {
      right: rem(10);
      font-size: rem(17.25);
    }
    &:before {
      content: '';
      position: absolute;
      top: rem(-7);
      left: rem(-30);
      z-index: -1;
      width: rem(44);
      height: rem(44);
      background-color: var(--white);
      border-radius: 50%;
      @media (min-width: 992px) {
        top: rem(0);
        left: rem(-56);
        width: rem(82);
        height: rem(82);
      }
      @media (min-width: 1600px) {
        top: rem(15);
        width: rem(100);
        height: rem(100);
      }
    }
  }
}