.tools {
  display: flex;
  justify-content: space-between;
  padding: rem(43) rem(28) rem(47);
  background-color: rgba(93, 134, 116, .06);
  border: rem(.914939) solid rgba(93, 134, 116, .3);
  border-radius: rem(9.14939);
  @media (max-width: 992px) {
    flex-direction: column;
  }
  @media (min-width: 992px) {
    padding: rem(71.39) rem(48) rem(87.8) rem(57.64);
  }
  @media (min-width: 1600px) {
    padding: rem(78) rem(59) rem(78) rem(63);
  }
  &-wrap {
    padding-bottom: rem(60.7);
    @media (min-width: 992px) {
      padding-bottom: rem(47);
    }
    @media (min-width: 1600px) {
      padding-bottom: rem(53);
    }
  }
  &__head {
    width: 100%;
    @media (min-width: 992px) {
      max-width: rem(700.802);
      margin-right: rem(70);
    }
    @media (min-width: 1600px) {
      max-width: rem(731);
    }
  }
  &__headline {
    color: var(--green5);
    b {
      color: var(--orange);
    }
  }
  &__desc {
    position: relative;
    display: flex;
    padding-top: rem(35);
    width: 100%;
    max-width: rem(317);
    font-size: rem(16);
    line-height: 1.47;
    letter-spacing: rem(.137241);
    color: var(--green2);
    @media (min-width: 992px) {
      max-width: rem(540);
      font-size: rem(18);
    }
    @media (min-width: 1600px) {
      max-width: rem(591);
      padding-top: rem(40);
      font-size: rem(22);
    }
    &:before {
      content: '';
      display: inline-block;
      width: 100%;
      max-width: rem(44);
      min-width: rem(44);
      height: rem(1);
      margin-top: rem(14);
      margin-right: rem(20);
      background-color: var(--green5);
      @media (min-width: 992px) {
        max-width: rem(87.83);
        min-width: rem(87.83);
        margin-right: rem(23.97);
      }
      @media (min-width: 1600px) {
        max-width: rem(96);
        min-width: rem(96);
        margin-right: rem(26);
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    @media (max-width: 992px) {
      padding-top: rem(42);
    }
    @media (min-width: 992px) {
      max-width: rem(436);
    }
    @media (min-width: 1600px) {
      max-width: rem(470);
    }
    li {
      padding-left: rem(15);
      font-size: rem(16);
      line-height: 1.47;
      letter-spacing: rem(.137241);
      color: var(--green2);
      list-style-image: url("../img/ico/Check.svg");
      @media (min-width: 992px) {
        font-size: rem(18);
      }
      @media (min-width: 1600px) {
        font-size: rem(22);
      }
      &:not(:last-child) {
        padding-bottom: rem(24);
      }
    }
  }
  &__btn {
    width: fit-content;
    min-width: rem(307);
    @media (max-width: 992px) {
      min-width: rem(318);
      margin-top: rem(53);
      font-size: rem(16);
    }
    @media (max-width: 1600px) {
      height: rem(50);
      min-width: rem(335);
    }
  }
}