.hero {
  .shape {
    @media (max-width: 768px) {
      width: rem(90);
    }
  }
  &__desc {
    max-width: rem(983);
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: 992px) {
      justify-content: center;
    }
  }
  .btn.orange-button {
    margin-left: auto;
    @media (max-width: 1200px) {
      margin-left: auto;
      margin-right: auto;
    }
    @media (max-width: 768px) {
      font-size: rem(18);
    }
  }
}
.title-block-description {
  @media (min-width: 992px) {
    text-align: left !important;
  }
}
//new-main
.hero-main {
  h1 {
    max-width: rem(420);
    margin-left: auto;
    margin-right: auto;
    @media (min-width: 768px) {
      max-width: rem(560);
    }
    @media (min-width: 1200px) {
      max-width: rem(855);
    }
  }
  .title-block-description {
    max-width: rem(440);
    @media (min-width: 1600px) {
      max-width: rem(509);
    }
  }
  .hero__desc {
    max-width: rem(720);
    @media (min-width: 1600px) {
      max-width: rem(840);
    }
  }
  .title-block-button {
    @media (min-width: 992px) {
      margin-top: 0 !important;
    }
  }
}

.main-new-page {
  .header-image-1 {
    @media (max-width: 370px) {
      height: rem(1300);
    }
    @media (max-width: 450px) {
      height: rem(1260);
    }
    @media (max-width: 525px) {
      height: rem(1300);
    }
  }
}